  reunionesService.uploadadjunto = function(file){
    let formData = new FormData();
    formData.append('archivo', file);

    return new Promise(function(resolve, reject) {
      

      axios.post(configService.apiUrl + '/uploadAdjuntoReunion' , formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(function(res) {

        resolve(res)      
        
      })
      .catch(function(err) {     

        resolve(err)
           
      })
    })

  }

  export default reunionesService