        <v-btn color="primary" outlined small @click="openRespuesta()" class="mx-1">Respuesta</v-btn>
        <v-btn color="primary" outlined small @click="previewDoc()" class="mx-1">Previsualizar respuesta</v-btn>
        <v-spacer></v-spacer>
        <!-- <v-btn small color="primary" dark>Guardar</v-btn> -->
      </v-row>
    </v-card-text>
  </v-card>
  <!-- Dialog respuesta -->
  <v-dialog v-model="dialog" width="900" persistent>
    <v-card>
      <v-card-title>
        <v-layout class="justify-center align-center">
          Proyectar respuesta
          <v-spacer></v-spacer>
          <v-btn @click="dialog=!dialog" outlined color="primary" small>Cerrar</v-btn>
          <v-btn color="primary" small class="ml-2" :disabled="usuarionormal" @click="saveRespuesta()">Guardar</v-btn>
        </v-layout>
      </v-card-title>
      <v-card-text>
        <vue-editor
          v-model="respuesta"
          id="editor2"
          style="width: 100%"
          :editor-toolbar="customToolbar"
          :disabled="usuarionormal"
          >
        </vue-editor>
        <v-file-input
          class="mt-4"
