              >
                <v-toolbar-title>Comentarios</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>

              <v-list three-line>
                <template v-for="(item, index) in items">
                  <v-list-item
                    :key="index"
                  >
                    <v-list-item-avatar>
