                </template>
              </v-list>
            </v-card>
            </v-col>
          </v-row>
        </v-container> -->
      </v-card-text>
    </v-card>
  </v-dialog>
  <!-- Dialog comentarios -->
  <v-dialog
  v-model="dialog_comment"
  persistent
  max-width="400">
    <v-card
      class="mx-auto"
    >
      <v-card-title class="headline" >
          <v-layout class="align-center justify-center">
          </v-layout>
      </v-card-title>
      <v-card-text>
        <div>
          <v-layout class="headline font-weight-bold align-center justify-center mb-2">
            Añadir comentario
          </v-layout>
          <v-textarea
            outlined
            label="Comentario"
            v-model="txtcomentario"
          ></v-textarea>
          <v-layout class="align-center justify-center">
            <v-btn color="success" @click="saveComentario()"><v-icon class="mr-1">save</v-icon>Guardar</v-btn>
          </v-layout>
        </div>
        <!-- <v-list three-line>
          <template v-for="(item, index) in items_comments">
            <v-divider
              v-if="item.divider"
              :key="index"
              :inset="item.inset"
            ></v-divider>

            <v-list-item
              v-else
              :key="index"
            >
              <v-list-item-avatar>
                <v-avatar color="primary">
                  <span class="white--text headline">{{iniciales}}</span>
                </v-avatar>
              </v-list-item-avatar>

              <v-list-item-content>
                <v-list-item-title v-html="item.title">
                </v-list-item-title>
