                  </v-layout>
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </template>
        </v-list> -->
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="error" text @click="dialog_comment = false">Cerrar</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
  <div id="source-html" v-show="false">    
    <h1>
      <center>Respuesta PQR #21</center>
    </h1>
    <h2>Hechos</h2>
    <p>
      Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. 
      Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. 
      Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. 
      Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum
      <!-- Artificial Intelligence(AI) is an emerging technology
      demonstrating machine intelligence. The sub studies like <u><i>Neural
              Networks</i>, <i>Robatics</i> or <i>Machine Learning</i></u> are
      the parts of AI. This technology is expected to be a prime part
      of the real world in all levels. -->
    </p>
  </div>
  </div>
</template>
<script>
import PqrService from "../../servicios/PqrService"
import { VueEditor } from 'vue2-editor'
export default {
  components: { VueEditor },
  data () {
    return {
      iniciales: '',
      txtfecha: '',
      txtnombre: '',
      txtdireccion: '',
      txttelefono: '',
      txtemail: '',
      txttipocomunica: '',
      txtasunto: '',
      txtprioridad: '',
      txtmediosolicita: '',
      txtconfidencialidad: '',
      txtmunicipio: '',
      txtcomentario: '',
      txtadjuntos: [],
      valBarcode: '',
      usuarionormal: true,
      id_estado: '',
      txtestado: '',
      estado_color: '',
      dialog: false,
      dialog_comment: false,
      respuesta: '',
      customToolbar: [
        [{ header: [false, 1, 2, 3, 4, 5, 6] }],
        ["bold", "italic", "underline", "strike"], // toggled buttons
        [
          { align: "" },
          { align: "center" },
          { align: "right" },
          { align: "justify" }
        ],
        ["blockquote", "code-block"],
        [{ list: "ordered" }, { list: "bullet" }, { list: "check" }],
        [{ indent: "-1" }, { indent: "+1" }], // outdent/indent
        [{ color: [] }, { background: [] }], // dropdown with defaults from theme
        ["image","attachment"]
      ],
      items_adjuntos: [],
      items_comments: []
    }
  },
  created () {
    var nombre = this.$session.get('nombre').split(" ",2)
    this.iniciales = nombre[0].substring(0,1) + nombre[1].substring(0,1)
  },
  async mounted () {
    this.getData()
    this.getResponsable()
  },
  methods: {
    downloadAdjunto(url) {
      const win = window.open(url, '_blank')
      win.focus()
    },
    async deleteAdjunto(item) {
      var nombre = item.ruta_adjunto.substring(57)
      const {data} = await PqrService.deleteFile(nombre, item.id)
      if (data.tipo === 'success') {
        this.openRespuesta()
      }
    },
    async getResponsable () {
      try {
        const {data} = await PqrService.getResponsablepqr(this.$session.get('usuarioid'),this.$route.params.id)
        this.usuarionormal = data.respuesta
      } catch (error) {
        console.log(error)
      }
    },
    previewDoc () {
      var header = "<html xmlns:o='urn:schemas-microsoft-com:office:office' "+
        "xmlns:w='urn:schemas-microsoft-com:office:word' "+
        "xmlns='http://www.w3.org/TR/REC-html40'>"+
        "<head><meta charset='utf-8'><title>Export HTML to Word Document with JavaScript</title></head><body>"
      // var body = "<h1><center>Artificial Intelligence</center></h1>"+
      // "<h2>Overview</h2>"+
      // "<p>Artificial Intelligence(AI) is an emerging technology demonstrating machine intelligence."+
      // "The sub studies like <u><i>Neural Networks</i>, <i>Robatics</i> or <i>Machine Learning</i></u> are "+
      // "the parts of AI. This technology is expected to be a prime part"+
      // " of the real world in all levels.</p>"
      var footer = "</body></html>"
      // var sourceHTML = header+body+footer
      var sourceHTML = header+document.getElementById("source-html").innerHTML+footer;
      
      var source = 'data:application/vnd.ms-word;charset=utf-8,' + encodeURIComponent(sourceHTML)
      var fileDownload = document.createElement("a")
      document.body.appendChild(fileDownload)
      fileDownload.href = source
      fileDownload.download = 'respuesta.doc'
      fileDownload.click()
      document.body.removeChild(fileDownload)
    },
    async saveComentario() {
      // var info = {
      //   id_reclamo: this.$route.params.id,
      //   comentario: this.respuesta,
      //   id_usuario: this.$session.get('usuarioid')
      // }
      // try {
      //   const {data} = await PqrService.saveRespuesta(info,this.txtadjuntos)
      //   console.log(data)
      //   // this.$toasted.success('Registro actualizado correctamente', { icon: 'done', duration: 3000 })
      //   // this.$toasted.error('Error al guardar archivo oficio', { icon: 'done', duration: 3000 })
      // } catch (error) {
      //   console.log(error)
      // }
    },
    async saveRespuesta() {
      var info = {
        id_reclamo: this.$route.params.id,
        comentario: this.respuesta,
        id_usuario: this.$session.get('usuarioid')
      }
      try {
        const {data} = await PqrService.saveRespuesta(info,this.txtadjuntos)
        console.log(data)
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'done', duration: 3000 })
        } else {
          this.$toasted.error(data.respuesta, { icon: 'done', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
        this.dialog = false
      }
    },
    async openRespuesta() {
      const {data} = await PqrService.getRespuesta(this.$route.params.id)
      console.log(data)
      if (data.length > 0) {
        this.respuesta = data[0].comentario
        this.items_adjuntos = data[0].adjuntos
      }
      this.dialog = true
      this.txtadjuntos = []
    },
    async getData() {
      const {data} = await PqrService.listPqr(this.$route.params.id)
      this.valBarcode = data.pqrs_reclamos[0].consecutivo
      this.txtfecha = data.pqrs_reclamos[0].fecha_creacion
      this.txtnombre = data.pqrs_reclamos[0].remitente
      this.txtdireccion = data.pqrs_reclamos[0].direccion
      this.txttelefono = data.pqrs_reclamos[0].telefono
      this.txtemail = data.pqrs_reclamos[0].email
      this.txttipocomunica = data.pqrs_reclamos[0].descripcion
      this.txtasunto = data.pqrs_reclamos[0].asunto
      this.txtprioridad = data.pqrs_reclamos[0].prioridad
      this.id_estado = data.pqrs_reclamos[0].id_estado
      this.txtmediosolicita = data.pqrs_reclamos[0].medio_solicitante
      this.txtmunicipio = data.pqrs_reclamos[0].municipio
      if ( parseInt(data.pqrs_reclamos[0].confidencialidad) === 0 ) {
        this.txtconfidencialidad = 'No'
      } else { this.txtconfidencialidad = 'Si' }
      switch (parseInt(this.id_estado)) {
        case 0:
          this.txtestado = 'Abierta'
          this.estado_color = '#5E35B1'
        break;
        case 1:
          this.txtestado = 'Asignado'
          this.estado_color = '#58af58'
        break;
        case 2:
          this.txtestado = 'En desarrollo'
          this.estado_color = '#FFC61A'
        break;
        case 3:
          this.txtestado = 'Cerrada'
          this.estado_color = '#F44336'
        break;
      }
    }
  },
  computed: {
    textBtnCommits () {
      return 'Ver historial'
    },
  },
  watch: {

  }
}
</script>
<style scoped>
.theme--dark.v-text-field--solo > .v-input__control > .v-input__slot {
