<template>
	<v-container fluid>		
		 <v-flex>
			<div style="padding-bottom:10px" v-if="readonlyData==false"> 
				<v-btn fab dark small color="primary" @click="dialog = true, edit=0,itemactual=''" >
					<v-icon dark>add</v-icon>				
				</v-btn>
			</div>		
			<v-card >
				<v-card-title class="justify-center">DESPLAZAMIENTOS</v-card-title>
				<v-card-text>
					<v-text-field
						v-model="search"
						append-icon="search"
						label="Buscar"
						single-line
						hide-details
					></v-text-field>
				</v-card-text>
				<v-layout column style="padding-top:16px">
		            <v-data-table :headers="headers" :items="data" :items-per-page="5" 
						:disable-sort="true" :search="search">

						<template v-slot:item.action="{ item }">
							<v-icon
								small
								class="mr-5"
								color="primary" 
								:disabled="loading"
								@click="dialog = true, edit=1, itemactual=item"								
							>
								edit
							</v-icon>		
					  	</template>					              
		            </v-data-table>		            	
		          </v-layout>
			</v-card>
		</v-flex>

		<v-dialog v-model="dialog" persistent scrollable max-width="1200px">

          	<v-card>
				<v-card-title>
					<span class="headline">{{ formTitle }}
						<div class="warning-msg">
							<i class="fa fa-warning"><v-icon>warning</v-icon></i>
							Recuerde dar click en el botón de Guardar. Todos los procesos que se realicen 
							son temporales y se pueden perder, hasta que se le indique al sistema que 
							deben ser guardados definitivamente.
						</div>						
					</span>					
				</v-card-title>  

				<v-card-text>				

					<v-container  grid-list-md>
						<v-layout wrap column>	

							<v-row justify="space-around" align="center">								
								<v-chip
									class="ma-2"
									:input-value="e1==1 ? true: false"
									:class="{ 'primary': e1==1 }"
									filter
									@click="e1=1"
								>
									1. RUBRO PRESUPUESTAL
								</v-chip>										
							
								<v-chip
									class="ma-2"
									:input-value="e1==2 ? true: false"
									:class="{ 'primary': e1==2 }"
									filter
									@click="e1=2"
									:disabled="totalrubros > 0 ? false : true"
									
								>
									2. SOLICITUD
								</v-chip>

								<v-chip
									class="ma-2"
									:input-value="e1==2 ? true: false"
									:class="{ 'primary': e1==3 }"
									filter
									@click="e1=3"
								>
									3. LUGARES
								</v-chip>

								<v-chip
									class="ma-2"
									:input-value="e1==3 ? true: false"
									:class="{ 'primary': e1==4 }"
									filter
									@click="e1=4"	
									v-if="edit!=0"								

								>
									4. VISTO BUENO PLANEACIÓN
								</v-chip>

								<v-chip
									class="ma-2"
									:input-value="e1==4 ? true: false"
									:class="{ 'primary': e1==5 }"
									filter
									@click="e1=5"	
									v-if="edit!=0 && voboplaneacion==20"								

								>
									5. LIQUIDACIÓN DE VIÁTICOS
								</v-chip>

								<v-chip
									class="ma-2"
									:input-value="e1==5 ? true: false"
									:class="{ 'primary': e1==6 }"
									filter
									@click="e1=6"	
									v-if="edit!=0"								

								>
									6. VISTO BUENO FINANCIERA
								</v-chip>

								<v-chip
									class="ma-2"
									:input-value="e1==6 ? true: false"
									:class="{ 'primary': e1==7 }"
									filter
									@click="e1=7"	
									v-if="edit!=0"								

								>
									7. VISTO BUENO DIRECCIÓN
								</v-chip>

								<v-chip
									class="ma-2"
									:input-value="e1==7 ? true: false"
									:class="{ 'primary': e1==8 }"
									filter
									@click="e1=8"	
									v-if="edit!=0"								

								>
									8. DATOS PRESUPUESTO
								</v-chip>

								<v-chip
									class="ma-2"
									:input-value="e1==8 ? true: false"
									:class="{ 'primary': e1==9 }"
									filter
									@click="e1=9"	
									v-if="edit!=0 && enviovobo==1 && esUsuarioTES==true && (vobopresupuesto==20 || vobopresupuesto=='20')"								

								>
									9. PAGO TESORERÍA
								</v-chip>
							</v-row>

							<v-form v-model="valid" ref="form">								
								<v-stepper v-model="e1" :vertical="false">
									<v-stepper-header>										
										<v-stepper-step step="1" :editable="true"></v-stepper-step>
										<v-divider></v-divider>
										<v-stepper-step step="2" :editable="totalrubros > 0"></v-stepper-step>
										<v-divider></v-divider>
										<v-stepper-step step="3" :editable="true"></v-stepper-step>
										<v-divider></v-divider>
										<v-stepper-step step="4" :editable="true" v-if="edit!=0"></v-stepper-step>
										<v-divider></v-divider>
										<v-stepper-step step="5" :editable="true" v-if="edit!=0 && voboplaneacion==20"></v-stepper-step>
										<v-divider></v-divider>
										<v-stepper-step step="6" :editable="true" v-if="edit!=0 && voboplaneacion==20"></v-stepper-step>
										<v-divider></v-divider>
										<v-stepper-step step="7" :editable="true" v-if="edit!=0 && voboplaneacion==20"></v-stepper-step>
										<v-divider></v-divider>
										<v-stepper-step step="8" :editable="true" v-if="edit!=0 && voboplaneacion==20"></v-stepper-step>
										<v-divider></v-divider>
										<v-stepper-step step="9" :editable="true" v-if="edit!=0 && enviovobo==1 && esUsuarioTES==true && (vobopresupuesto==20 || vobopresupuesto=='20')"></v-stepper-step>
									</v-stepper-header>
									
									<v-stepper-items>

										<v-stepper-content step="1">
											<!-- rubro presupuestal -->
											<v-flex sm12>
												<v-btn class="ma-2" color="primary" 
													text                                            
													@click="selected = [],dialog_addrubro = true"
													v-if="enviovobo==0 && readonlyData==false"
												>
													Seleccionar Rubro
													<v-icon
														dark 
														right																														
													>
														send
													</v-icon>
												</v-btn>
												<div style="width:96%;text-align: center;padding-left: 10px;">																
													<v-data-table :headers="headerrubrogen" 
														:items="arrRubrosGen" 
														:disable-sort="true" :disable-pagination="true" 
														:hide-default-footer="true"
													>														

														<template v-slot:item.valor="{ item }">
															<span>{{ item.valor |  currency({
																	symbol: '$',
																	thousandsSeparator: '.',
																	fractionCount: '0',
																	fractionSeparator: ',',
																	symbolPosition: 'front',
																	symbolSpacing: true
																})}}
															</span>
														</template>

														<template v-slot:item.comprometido="{ item }">
															<span>{{ item.comprometido |  currency({
																	symbol: '$',
																	thousandsSeparator: '.',
																	fractionCount: '0',
																	fractionSeparator: ',',
																	symbolPosition: 'front',
																	symbolSpacing: true
																})}}
															</span>
														</template>	

														<template v-slot:item.saldo="{ item }">
															<span>{{ item.saldo |  currency({
																	symbol: '$',
																	thousandsSeparator: '.',
																	fractionCount: '0',
																	fractionSeparator: ',',
																	symbolPosition: 'front',
																	symbolSpacing: true
																})}}
															</span>
														</template>	

														<template v-slot:item.action="{ item }">
															<v-icon
																small
																class="mr-5"
																color="primary" 
																:disabled="loading"
																@click="eliminaItem(item)"
																v-if="enviovobo==0 && readonlyData==false"
															>
																delete
															</v-icon>		
														</template>	

													</v-data-table>
												</div>										
											</v-flex>

											<br>
											<h3>Total Disponible: {{ `$ ${ number_format(totalrubros,2) }` }} </h3>
										</v-stepper-content>

										<v-stepper-content step="2">
											<v-layout wrap>
												<v-flex sm6>
													<v-select
														:items="tipobeneficiarioSel"
														v-model="tipobeneficiario"
														label="Tipo de Beneficiario"
														item-value="value"
														item-text="text"
														:return-object="false"
														@change="limpiarBeneficiario"
														:readonly="enviovobo==1 || readonlyData==true"
													>									
													</v-select>    									
												</v-flex>

												<v-flex sm6>
													<v-autocomplete
														:items="arrBeneficiarios"
														item-value="codigo"
														item-text="nombrecompleto"
														v-model="selPersona"
														label="Seleccione el Beneficiario"														
														:return-object="true"
														v-if="(tipobeneficiario=='T' || tipobeneficiario=='C') && enviovobo==0 && readonlyData==false"
														@change="traeDatosPersona"																			
													>									
													</v-autocomplete>    									
												</v-flex>

												<v-flex sm3>
													<v-autocomplete
														:items="arrTiposId"
														item-value="codigo"
														item-text="descripcion"
														v-model="tipoidentcodigo"
														label="Tipo Identificación"
														:rules="[rules.required]"
														:return-object="true"
														:readonly="tipobeneficiario=='T' || tipobeneficiario=='C' || enviovobo==1 || readonlyData==true"																			
													>									
													</v-autocomplete>
												</v-flex>    
												<v-flex sm3>
													<v-text-field v-model="identificacion" 
														:readonly="tipobeneficiario=='T' || tipobeneficiario=='C' || enviovobo==1 || readonlyData==true" 
														label="identificación" :rules="[rules.required]">
													</v-text-field>			
												</v-flex>
												<v-flex sm6>
													<v-text-field v-model="beneficiario" 
														:readonly="tipobeneficiario=='T' || tipobeneficiario=='C' || enviovobo==1 || readonlyData==true" 
														label="Nombre Beneficiario" :rules="[rules.required]">
													</v-text-field>		
												</v-flex>												

												<v-flex sm12>
													<v-text-field v-model="direccion" 
														:readonly="tipobeneficiario=='T' || tipobeneficiario=='C' || enviovobo==1 || readonlyData==true" 
														label="Dirección" :rules="[rules.required]">
													</v-text-field>
												</v-flex>

												<v-flex sm4>
													<v-text-field v-model="telefono" 
														:readonly="tipobeneficiario=='T' || tipobeneficiario=='C' || enviovobo==1 || readonlyData==true" 
														label="Teléfono" :rules="[rules.required]">
													</v-text-field>
												</v-flex>

												<v-flex sm4>
													<v-menu
														v-model="menuDate"
														:close-on-content-click="false"
														:nudge-right="40"
														transition="scale-transition"
														offset-y																																
													>
														<template v-slot:activator="{ on }">
															<v-text-field
																v-model="fechanacimiento"
																label="Fecha Nacimiento"
																prepend-icon="event"
																readonly
																v-on="on"                                                																																			
															></v-text-field>
														</template>
														<v-date-picker 
															v-model="fechanacimiento" @input="menuDate = false"
																:readonly="tipobeneficiario=='T' || tipobeneficiario=='C' || enviovobo==1 || readonlyData==true"  
																:type="'date'" :rules="[rules.required]"
														>
														</v-date-picker>
													</v-menu>													
												</v-flex>

												<v-flex sm4>
													<v-text-field v-model="email" 
														:readonly="tipobeneficiario=='T' || tipobeneficiario=='C' || enviovobo==1 || readonlyData==true" 
														label="Email" :rules="[rules.required,rules.email]">
													</v-text-field>
												</v-flex>

												<v-flex sm6>
													<v-autocomplete
														:items="arrCargos"
														item-value="codigo"
														item-text="nombre"
														v-model="cargocodigo"
														label="Cargo"
														:rules="[rules.required]"
														:readonly="tipobeneficiario=='T' || tipobeneficiario=='C' || enviovobo==1 || readonlyData==true"
														:return-object="false"					
													>									
													</v-autocomplete>
												</v-flex>

												<v-flex sm6>
													<v-text-field v-model="valorbaseben"
														label="Valor Base" 
														:rules="[rules.required]"
														prefix='$ '														
														:readonly="tipobeneficiario=='T' || tipobeneficiario=='C' || enviovobo==1 || readonlyData==true"
														@blur="formatearValor(valorbaseben,'valorbaseben')"
														@focus="quitarFormato('valorbaseben')"
													>
													</v-text-field>
												</v-flex>

												<v-flex sm6>
													<v-text-field v-model="valorcontrato" 														
														v-if="tipobeneficiario=='C'" 
														readonly
														label="Valor Contrato" 														
														prefix='$ '
														@blur="formatearValor(valorcontrato,'valorcontrato')"
														@focus="quitarFormato('valorcontrato')"
													>
													</v-text-field>
												</v-flex>

												<v-flex sm6>
													<v-text-field v-model="mesescontrato" 
														type="number"
														v-if="tipobeneficiario=='C'" 
														readonly
														label="Meses Contrato">
													</v-text-field>
												</v-flex>

												<v-flex sm12>
													<v-text-field v-model="objetivogeneral" 
														label="Objetivo General" :rules="[rules.required]"
														:readonly="enviovobo==1 || readonlyData==true">
													</v-text-field>
												</v-flex>
												
												<v-flex sm6>
													<v-select
														:items="sinoSel"
														v-model="pagoanticipado"
														label="Pago Anticipado"
														item-value="value"
														item-text="text"
														:return-object="false"
														v-if="tipobeneficiario=='C'"
														:readonly="enviovobo==1 || readonlyData==true"
													>									
													</v-select>    									
												</v-flex>

												<v-flex sm6>
													<v-select
														:items="sinoSel"
														v-model="reembolso"
														label="Reembolso"
														item-value="value"
														item-text="text"
														:return-object="false"
														v-if="tipobeneficiario=='C'"
														:readonly="enviovobo==1 || readonlyData==true"
													>									
													</v-select>    									
												</v-flex>

												<v-flex sm2>
													<v-select
														:items="sinoSel"
														v-model="requieretiquetes"
														label="Requiere Tiquetes"
														item-value="value"
														item-text="text"
														:return-object="false"
														@change="limpiavalores('requieretiquetes')"	
														:readonly="enviovobo==1 || readonlyData==true"													
													>									
													</v-select>    									
												</v-flex>
												
												<v-flex sm2>
													<v-text-field v-model="valortiquetes" 
														label="Valor Tiquetes" 
														:readonly="!tiquetesok || enviovobo==1 || readonlyData==true"														
														prefix='$ '
														@blur="formatearValor(valortiquetes,'valortiquetes')"
														@focus="quitarFormato('valortiquetes')"														
													>
													</v-text-field>
												</v-flex>

												<v-flex sm2>
													<v-select
														:items="sinoSel"
														v-model="viaticos"
														label="Viáticos"
														item-value="value"
														item-text="text"
														:return-object="false"	
														@change="limpiavalores('viaticos')"	
														:readonly="enviovobo==1 || readonlyData==true"													
													>									
													</v-select>    									
												</v-flex>											

												<v-flex sm2>
													<v-text-field v-model="valorviaticos" 
														:readonly="!viaticosok || enviovobo==1 || readonlyData==true"
														label=" Valor Viáticos" 														
														prefix='$ '
														@blur="formatearValor(valorviaticos,'valorviaticos')"
														@focus="quitarFormato('valorviaticos')"
													>
													</v-text-field>
												</v-flex>

												<v-flex sm2>
													<v-select
														:items="sinoSel"
														v-model="gastosviaje"
														label="Gastos de Viaje"
														item-value="value"
														item-text="text"
														:return-object="false"	
														@change="limpiavalores('gastosviaje')"	
														:readonly="enviovobo==1 || readonlyData==true"													
													>									
													</v-select>    									
												</v-flex>

												<v-flex sm2>
													<v-text-field v-model="valorgastosviaje" 
														:readonly="!gastosviajeok || enviovobo==1 || readonlyData==true"
														label=" Valor Gastos de Viajes"													
														prefix='$ '
														@blur="formatearValor(valorgastosviaje,'valorgastosviaje')"
														@focus="quitarFormato('valorgastosviaje')"
													>
													</v-text-field>
												</v-flex>

												<v-flex sm12>
													<v-text-field v-model="observacion" 
														label="Observación"
														:readonly="enviovobo==1 || readonlyData==true"
													>
													</v-text-field>
												</v-flex>
											</v-layout>	
										</v-stepper-content>										

										<v-stepper-content step="3">
											<!-- lugares -->											
											
											<div style="padding-bottom:10px" v-if="enviovobo==0 && readonlyData==false"> 
												<v-btn fab dark small color="primary" 
													@click="dialog_destinos = true, editdestinos=0,itemactualdes=''" 
													
												>
													<v-icon dark>add</v-icon>				
												</v-btn>
											</div>

											<v-text-field
												v-model="searchDestinos"
												append-icon="search"
												label="Buscar"
												single-line
												hide-details
											></v-text-field>				

											<v-layout column style="padding-top:16px">
												<v-data-table :headers="headdestinos" :items="arrDestinos" 
													:items-per-page="5" 
													:disable-sort="true" 
													:search="searchDestinos">

													<template v-slot:item.tarifa="{ item }">
														<span>{{ item.tarifa |  currency({
																symbol: item.simbolomoneda,
																thousandsSeparator: '.',
																fractionCount: '0',
																fractionSeparator: ',',
																symbolPosition: 'front',
																symbolSpacing: true
															})}}
														</span>
													</template>

													<template v-slot:item.tasa="{ item }">
														<span>{{ item.tasa |  currency({
																symbol: ' $',
																thousandsSeparator: '.',
																fractionCount: '0',
																fractionSeparator: ',',
																symbolPosition: 'front',
																symbolSpacing: true
															})}}
														</span>
													</template>

													<template v-slot:item.tarifapesos="{ item }">
														<span>{{ item.tarifapesos |  currency({
																symbol: ' $',
																thousandsSeparator: '.',
																fractionCount: '0',
																fractionSeparator: ',',
																symbolPosition: 'front',
																symbolSpacing: true
															}) }}
														</span>
													</template>

													<template v-slot:item.action="{ item }">
														<v-icon
															small
															class="mr-5"
															color="primary" 
															:disabled="loading"
															@click="dialog_destinos = true, editdestinos=1, itemactualdes = item"
															v-if="enviovobo==0 && readonlyData==false"
														>
															edit
														</v-icon>

														<v-icon
															small
															class="mr-5"
															color="primary" 
															:disabled="loading"
															@click="eliminaItemdestinos(item)"
															v-if="enviovobo==0 && readonlyData==false"
														>
															delete
														</v-icon>		
													</template>					              
												</v-data-table>		
												
												<br>
												<h3>&nbsp;&nbsp;Total Dias de Viaje: {{ totaldiasviaje }} &nbsp;&nbsp;&nbsp;&nbsp; Valor Total  {{ `$ ${ totaltarifasviajes }` }} </h3>            	
											</v-layout>

										</v-stepper-content>

										<v-stepper-content step="4">
											<!-- Vobos -->
											<v-layout column style="padding-top:16px">
												<v-card
													max-width="1100"
													width="90%"
													class="mx-auto"
													elevation="2"
												>
													<v-card-title class="justify-center">
														Planeación
													</v-card-title>
													<v-card-text>
														<v-form v-model="validvbpla" ref="formvbpla">
