			},
			
			envioNotificaciones(values){ // mensajenotificacion,asuntocorreo, mensajecorreo){
				
				//se envia notificacion al usuario responsable
				var codigopersona = 0
				var codigoopcion = 47 //Desplazamientos
				var numerointerno = this.identificacion				

				for (let index = 0; index < this.usuariossistema.length; index++) {
					const element = this.usuariossistema[index]

					if(this.usuario == element.usuarioid){

						codigopersona = element.codigopersonal
						break
					}
					
				}

				if(codigopersona != '' && codigopersona != 0){
				
					this.$root.$emit('enviarNotificaciones', {codigopersona: codigopersona, mensajenotificacion: values.mensajenotificacion, 
															asuntocorreo: values.asuntocorreo, mensajecorreo: values.mensajecorreo,
															codigoopcion:codigoopcion,numerointerno:numerointerno})				
				}

				for (let index = 0; index < this.usuariosPlanViaticos.length; index++) {
					const element = this.usuariosPlanViaticos[index]

					codigopersona = element.codigopersonal

					this.$root.$emit('enviarNotificaciones', {codigopersona: codigopersona, mensajenotificacion: values.mensajenotificacion, 
														asuntocorreo: values.asuntocorreo, mensajecorreo: values.mensajecorreo,
														codigoopcion:codigoopcion,numerointerno:numerointerno})	
					
				}

			},					

