			saveitem(){
				
				var valviaticos = this.limpiarMoneda(this.valorviaticos)
				var valtiquetes = this.limpiarMoneda(this.valortiquetes)
				var valgastosviaje = this.limpiarMoneda(this.valorgastosviaje)
				var valbasegeneracion = this.limpiarMoneda(this.valorbaseben)
				var valcontrato = this.limpiarMoneda(this.valorcontrato)
				
				var totalenviajes = this.limpiarMoneda(this.totaltarifasviajes);	

				if(this.requieretiquetes == 'S' && (valtiquetes == '0' || valtiquetes == '')){

					this.showMessage(true,3000,'error','Debe ingresar el valor de los tiquetes sin es requerido')					
					return

				} else if (this.viaticos == 'S' && (valviaticos == '0' || valviaticos == '')){

					this.showMessage(true,3000,'error','Debe ingresar el valor de los viáticos sin es requerido')					
					return

				} else if (this.gastosviaje == 'S' && (valgastosviaje == '0' || valgastosviaje == '')){

					this.showMessage(true,3000,'error','Debe ingresar el valor de los gastos de viaje sin es requerido')					
					return

				}				

				var baseMoneda = parseFloat(valviaticos) + parseFloat(valtiquetes) + parseFloat(valgastosviaje) + parseFloat(totalenviajes)
				
				if (parseFloat(baseMoneda) > parseFloat(this.totalrubros)) {

					this.showMessage(true,3000,'error','El total de los items superan el saldo de los rubros')					
					return

				}

				var _this = this
                this.loading = true

				var datbit = ''	
				
				var data = {codigo:this.codigo,tipobeneficiario:this.tipobeneficiario,tipoidentcodigo:this.tipoidentcodigo,
							identificacion:this.identificacion,beneficiario:this.beneficiario,cargocodigo:this.cargocodigo,direccion:this.direccion,
							telefono:this.telefono,email:this.email,fechanacimiento:this.fechanacimiento,
							valorcontrato:valcontrato,mesescontrato:this.mesescontrato,usuario:this.usuario,
							requieretiquetes:this.requieretiquetes,objetivogeneral:this.objetivogeneral,
							viaticos:this.viaticos,pagoanticipado:this.pagoanticipado,reembolso:this.reembolso,
							valortiquetes:valtiquetes,gastosviaje:this.gastosviaje,valorgastosviaje:valgastosviaje,
							observacion:this.observacion,valorviaticos:valviaticos,valorbaseben:valbasegeneracion}

				if(this.edit){

					datbit = 'Beneficiario: ' + this.beneficiario					

					datbit = 'CODIGO: ' + this.codigo + ' (' + datbit + ')'

				} else {

                    datbit += 'BENEFICIARIO: ' + this.beneficiario

				}				

				var destinos = []
				var rubros = []

				for (let index = 0; index < this.arrRubrosGen.length; index++) {
					const element = this.arrRubrosGen[index]

					rubros.push({plan:element.planadquisicion,necesidad:element.codigo,
								actividad:element.secuenciaactividad,presupuesto:element.presupuesto,
								comprometido:element.comprometido,saldo:element.saldo})
					
				}

				for (let index = 0; index < this.arrDestinos.length; index++) {
					const element = this.arrDestinos[index]

					destinos.push({codigo:element.codigo,origennacional:element.origennacional,
								  origeninternacional:element.origeninternacional,destinonacional:element.destinonacional,
								  destinointernacional:element.destinointernacional,txtdestinoorigen:element.txtdestinoorigen,
								  txtdestinofinal:element.txtdestinofinal,fechaida:element.fechaida,fecharegreso:element.fecharegreso,
								  tipotransporte:element.tipotransporte,tarifa:element.tarifa,moneda:element.moneda,tasa:element.tasa,
								  tarifapesos:element.tarifapesos,totaltarifa:element.totaltarifa,diasviaje:element.diasviaje})
					
				}

                desplazamientosService.guardarData(data, destinos, rubros)
	              .then(function(res) {

	              	  if (res.response) {                           
                            
                          var error = res.response.data.message
                          _this.showMessage(true,3000,'error',error)

                      } else if(res.statusText){
	              	  		
	              	  		var mensaje = ''
							var accbitacora = ''

	              	  		if(_this.edit){
								mensaje = 'Actualizó'
								accbitacora = 'ACTUALIZACION'
							} else {
								mensaje = 'Agregó'
								accbitacora = 'CREACION'
							}

	              	  		_this.showMessage(true,3000,'success','Se ' + mensaje + ' el Desplazamiento con éxito')
							
							//if(_this.edit == 0){
								var mensajecorreo = "Cordial Saludo,<br><br> " +
													"Se " + mensaje + " el Desplazamiento para el Beneficiario: " + _this.beneficiario + 
													" con consecutivo " + _this.codigo +													
													"<br><br>Agradeciendo su atención,<br><br> " +                        
													"<b>SINCHI</b><br><br> " +
													"<i>Este es un servicio de notificaciones generado automáticamente por medio de " +
													"correo electrónico, no lo responda.</i> <br>"

								_this.envioNotificaciones({mensajenotificacion:'Se ' + mensaje + ' el Desplazamiento para el Beneficiario: ' 
															+ _this.beneficiario + ' con consecutivo ' + _this.codigo,  
														   asuntocorreo: 'Creación o Modificación de Desplazamiento',
														   mensajecorreo:mensajecorreo})
							//}	

							_this.edit = -1	              	  		
							_this.traerDataPrincipal()
							_this.resetForm()
							
							_this.agregaBitacora(accbitacora,datbit)
							_this.dialog = false

	              	  }else{	                                         	

	                  		_this.showMessage(true,3000,'error',res.message) 	                  	
	                  }

