			agregarRubros(){

				if(this.selected.length <= 0){

					this.showMessage(true,4000,'error','Debe seleccionar al menos un Rubro')
					return
				}

				var erroradd = ''
				var ok = 0
				var totalitemsadd = 0

				for (let index = 0; index < this.selected.length; index++) {
					const element = this.selected[index]

					ok = 1

					for (let k = 0; k < this.arrRubrosGen.length; k++) {
						const element2 = this.arrRubrosGen[k]

						if(element2.planadquisicion == element.planadquisicion && 
							element2.codigo == element.codigo && 
							element2.secuenciaactividad == element.secuenciaactividad && 
							element2.presupuesto == element.presupuesto){

								ok = 0
								erroradd +=  erroradd != '' ? ", " + element2.rubro : element2.rubro								
								break

						}
						
					}

					if(ok == 1)	{
						this.arrRubrosGen.push(element)					
						totalitemsadd += 1
					}
					
				}

				if(totalitemsadd == 0){

					this.showMessage(true,4000,'error','No se puede agregar ningún item porque ya fueron agregados')

				} else {
					
					if(erroradd != ''){

						this.showMessage(true,4000,'error',"Los siguientes Rubros ya fueron agregados: " + erroradd)

					}	

					this.dialog_addrubro = false
				}
			},

			eliminaItem(item){

				for (let index = 0; index < this.arrRubrosGen.length; index++) {
					const element = this.arrRubrosGen[index]

					if(item.planadquisicion == element.planadquisicion && 
						item.codigo == element.codigo && 
						item.secuenciaactividad == element.secuenciaactividad && 
						item.presupuesto == element.presupuesto){

							this.arrRubrosGen.splice(index,1)
							return

						}					
				}

