			saveitemDestino(){				

				var tiempodias = 0

				if(this.fechaida != '' && this.fecharegreso != ''){

					let fechaini = new Date(this.fechaida)
					let fechafin = new Date(this.fecharegreso)

					if(fechaini > fechafin){

						this.showMessage(true,4000,'error','La Fecha de Ida debe ser menor a la Fecha de Regreso')						
						return

					}

					let timeini = fechaini.getTime()
					let timefin = fechafin.getTime()

					var diff = timefin - timeini

					tiempodias = (diff/(1000*60*60*24))

					/*if(tiempodias > 30){

						this.showMessage(true,4000,'error','Los días de viaje no pueden ser superior a 30')						
						return

					}*/		
				}

				//se verifica que no coincida destino inicial con el final

				let totaldias = 0
				let totalvalortarifas = 0

				for (let index = 0; index < this.arrDestinos.length; index++) {
					const element = this.arrDestinos[index]

					if(this.origennacional == element.origennacional && 
						this.origeninternacional == element.origeninternacional &&
						this.destinonacional == element.destinonacional && 
						this.destinointernacional == element.destinointernacional &&
						this.codigodestino !=  element.codigo){

							this.showMessage(true,4000,'error','Ya existen los lugares de Destino y Origen seleccionados')						
							return

					} 

					if(this.codigodestino !=  element.codigo){
						totaldias += element.diasviaje	
						totalvalortarifas += parseFloat(element.tarifapesos)
					}	
					
				}
				
				let valactualtarifapesos = this.limpiarMoneda(this.tarifapesos)
				let totaldiasviajefin = parseInt(totaldias) + parseInt(tiempodias)
				totalvalortarifas += parseFloat(valactualtarifapesos)

				if(totaldiasviajefin > 30){

					this.showMessage(true,4000,'error','Los días de viaje de todos los destinos no pueden ser superior a 30')						
					return

				}

				this.totaldiasviaje = totaldiasviajefin					
				this.totaltarifasviajes = this.number_format(totalvalortarifas,2)

				if(this.codigodestino == 0){
					var max = this.arrDestinos.reduce((maximo, element) => Math.max(maximo, element.codigo), 0)
					max += 1

					this.arrDestinos.push({desplazamiento:this.codigo,codigo:max,
											origennacional:this.origennacional,
											origeninternacional:this.origeninternacional,
											destinonacional:this.destinonacional,
											destinointernacional:this.destinointernacional,
											txtdestinoorigen:this.txtdestinoorigen,
											txtdestinofinal:this.txtdestinofinal,
											fechaida:this.fechaida,
											fecharegreso:this.fecharegreso,
											tipotransporte:this.tipotransporte,
											txttipotransporte: (this.tipotransporte == 'F' ? 'FLUVIAL' : 
																(this.tipotransporte == 'T' ? 'TERRRESTRE' : 'AÉREO')),
											
											tarifa:this.limpiarMoneda(this.tarifa), 
											moneda:this.moneda,
											simbolomoneda:this.simbolomoneda,
											tasa:this.limpiarMoneda(this.tasa),
											tarifapesos:this.limpiarMoneda(this.tarifapesos),
											totaltarifa:this.limpiarMoneda(this.totaltarifa),
											diasviaje:tiempodias
										})
				
				} else {

					for (let index = 0; index < this.arrDestinos.length; index++) {
						const element = this.arrDestinos[index]

						if(this.codigodestino == element.codigo){

							element.origennacional = this.origennacional
							element.origeninternacional = this.origeninternacional
							element.destinonacional = this.destinonacional
							element.destinointernacional = this.destinointernacional
							element.txtdestinoorigen = this.txtdestinoorigen
							element.txtdestinofinal = this.txtdestinofinal
							element.fechaida = this.fechaida
							element.fecharegreso = this.fecharegreso
							element.tipotransporte = this.tipotransporte
							element.txttipotransporte = (this.tipotransporte == 'F' ? 'FLUVIAL' : 
														(this.tipotransporte == 'T' ? 'TERRRESTRE' : 'AÉREO'))

							element.tarifa = this.limpiarMoneda(this.tarifa)
							element.moneda = this.moneda
							element.simbolomoneda = this.simbolomoneda
							element.tasa = this.limpiarMoneda(this.tasa)
							element.tarifapesos = this.limpiarMoneda(this.tarifapesos)
							element.totaltarifa = this.limpiarMoneda(this.totaltarifa)
							element.diasviaje = tiempodias

							break							

						}						
					}
				}

				/*
				for (let index = 0; index < this.arrDestinos.length; index++) {
					const element = this.arrDestinos[index]

					totaldias += element.diasviaje	
					totalvalortarifas += parseFloat(element.tarifapesos)
												
				}*/
				

				this.cerrarDialogoDestino()

			},

			async agregarDestino(){

				if(this.tipodesplazamiento == 'o'){

					this.origennacional = ''
					this.origeninternacional = ''
					this.txtdestinoorigen = ''

					if(this.tipodestino == 'n'){
						this.origennacional = this.lugaractual						
						
					} else
						this.origeninternacional = this.lugaractual

					this.txtdestinoorigen = this.nombrelugaractual.trim().toUpperCase()

				} else {

					this.destinonacional = ''
					this.destinointernacional = ''
					this.txtdestinofinal = ''

					var valorbaseactual = this.limpiarMoneda(this.valorbaseben)

					if(this.tipodestino == 'n'){
						this.destinonacional = this.lugaractual					

						var tipociudad = this.destinonacional.substring(3,5)
						var encontro = 0

						for (let index = 0; index < this.tarifas.length; index++) {
							const element = this.tarifas[index]

							if(element.codigolugar == 1 && tipociudad == '01'){

								if (parseFloat(valorbaseactual) >= parseFloat(element.rangoinicial) && 
										parseFloat(valorbaseactual) <= parseFloat(element.rangofinal)){

									let totaltarifapesos = 0

									this.tarifa = this.number_format(element.valordiario,2)
									this.totaltarifa = parseFloat(this.diasviaje) * parseFloat(element.valordiario)
									this.moneda = 1
									this.simbolomoneda = '$ '
									this.tasa = this.number_format(this.tmptasa)
									totaltarifapesos = parseFloat(this.totaltarifa) * parseFloat(this.tmptasa)
									this.tarifapesos = parseFloat(this.tmptasa) == 0 ?  this.number_format(this.totaltarifa,2) : 
														this.number_format(totaltarifapesos,2)								
											
									encontro = 1
								}

							}else if(element.codigolugar == 2){
								
								if (parseFloat(valorbaseactual) >= parseFloat(element.rangoinicial) && 
										parseFloat(valorbaseactual) <= parseFloat(element.rangofinal)){

									let totaltarifapesos = 0

									this.tarifa = this.number_format(element.valordiario,2)
									this.totaltarifa = parseFloat(this.diasviaje) * parseFloat(element.valordiario)
									this.moneda = 1
									this.simbolomoneda = '$ '
									this.tasa = this.number_format(this.tmptasa)
									totaltarifapesos = parseFloat(this.totaltarifa) * parseFloat(this.tmptasa)
									this.tarifapesos = parseFloat(this.tmptasa) == 0 ?  this.number_format(this.totaltarifa,2) : 
														this.number_format(totaltarifapesos,2)								
											
									encontro = 1
								}
								

							}else if(element.codigolugar > 2)
								break

							if(encontro == 1)
								break
						}

					}else {
						
						this.destinointernacional = this.lugaractual

						this.tarifaspaises =[]
						var datatarifaspaises = await tarifaslugaresService.getRangosXPais(this.lugaractual)  
						
						if(datatarifaspaises.data){

							this.tarifaspaises = datatarifaspaises.data.data

						}
						
						this.tarifa = this.number_format(0)
						this.moneda = ''
						this.simbolomoneda = ''
						this.tasa = this.number_format(this.tmptasa)
						this.tarifapesos = this.number_format(0)

						if(this.tarifaspaises.length > 0){							

							for (let index = 0; index < this.tarifaspaises.length; index++) {
								const element = this.tarifaspaises[index]

								if (parseFloat(valorbaseactual) >= parseFloat(element.rangoinicial) && 
									parseFloat(valorbaseactual) <= parseFloat(element.rangofinal)){

										let totaltarifapesos = 0

										this.tarifa = this.number_format(element.valordiario,2)
										this.totaltarifa = parseFloat(this.diasviaje) * parseFloat(element.valordiario)
										this.moneda = this.tmpmoneda
										this.simbolomoneda = this.tmpsimbolomoneda
										this.tasa = this.number_format(this.tmptasa)
										totaltarifapesos = parseFloat(this.totaltarifa) * parseFloat(this.tmptasa)
										this.tarifapesos = parseFloat(this.tmptasa) == 0 ?  this.number_format(this.totaltarifa,2) : 
															this.number_format(totaltarifapesos,2)
										
										break
								}
								
							}

						}
					}

					this.txtdestinofinal = this.nombrelugaractual.trim().toUpperCase()
				}

				this.dialog_adddestino = false
			},

