			calcularValorDias(){

				var tiempodias = 0
				this.diasviaje = 0

				if(this.fechaida != '' && this.fecharegreso != ''){

					let fechaini = new Date(this.fechaida)
					let fechafin = new Date(this.fecharegreso)

					if(fechaini > fechafin){
						
						return

					}

					let timeini = fechaini.getTime()
					let timefin = fechafin.getTime()

					var diff = timefin - timeini

					tiempodias = (diff/(1000*60*60*24))

					this.diasviaje = tiempodias

					let tasaactual = this.limpiarMoneda(this.tasa)
					let tarifaactual = this.limpiarMoneda(this.tarifa) 
					let totalvalor = parseFloat(tiempodias) * parseFloat(tarifaactual)

					this.totaltarifa = this.number_format(totalvalor, 2)
					this.tarifapesos = parseFloat(tasaactual) == 0 ?  this.number_format(totalvalor, 2) :  
										this.number_format((parseFloat(tasaactual) * parseFloat(totalvalor)),2)
					
				}

			},

			habilitaLugares(){

				let valorbase = this.valorbaseben != '' ? this.limpiarMoneda(this.valorbaseben) : 0

				if(parseFloat(valorbase) > 0)
					return true
				else
					return false	

			},

			async traeUsuariosPerfiles(){

				this.usuariosPlanViaticos = []
				this.usuariosTH = []
				this.usuariosFI = []
