					//perfil valida en presupuesto
					let tmpperfilPRE = await usuariosService.getUsuariosXPerfil(10)

					//perfil valida en tesoreria
					let tmpperfilTES = await usuariosService.getUsuariosXPerfil(11)
										
					if(tmpperfil.data)
						this.usuariosPlanViaticos = tmpperfil.data.data				
						
					if(tmpperfilTH.data)
						this.usuariosTH = tmpperfilTH.data.data
						
					if(tmpperfilFI.data)
						this.usuariosFI = tmpperfilFI.data.data	

					if(tmpperfilDIR.data)
						this.usuariosDIR = tmpperfilDIR.data.data
						
					if(tmpperfilPRE.data)
						this.usuariosPRE = tmpperfilPRE.data.data

					if(tmpperfilTES.data)
						this.usuariosTES = tmpperfilTES.data.data	

					this.usuaactual = this.$session.get('usuarioid')	
					console.log('usuactual: ' + this.usuaactual)				
					
					for (let index = 0; index < this.usuariosPlanViaticos.length; index++) {
