			calcularViaticosAutomaticos(){
				
				if(this.tipoliquidacion == 'M'){

					this.valorliquidado = this.number_format(totalliquidaautomatico,2)		

				} else {
					var totalvaldestinos = 0
					var totdias = 0
					var valtarifaactual = 0
					var tasaactual = 0
					var diasviajeactual = 0

					var valortotal = 0
					var multiplica = 0

					for (let index = 0; index < this.arrDestinosOrdFecha.length; index++) {
						const element = this.arrDestinosOrdFecha[index]

						valtarifaactual = element.tarifa
						tasaactual = element.tasa
						diasviajeactual = element.diasviaje

						multiplica = tasaactual != 0 ? tasaactual : 1

						if(parseInt(totdias) < 16){

							if((parseInt(totdias) + parseInt(diasviajeactual)) > 15){

								let dias100 = 15 - parseInt(totdias)
								let tarifa100 = parseFloat(valtarifaactual) * parseInt(dias100) * multiplica

								let dias50 = parseInt(diasviajeactual) - parseInt(dias100)
								let tarifa50 = (parseFloat(valtarifaactual) / 2) * parseInt(dias50) * multiplica 

								valortotal += parseFloat(tarifa100) + parseFloat(tarifa50)							

							} else {

								valortotal += (parseFloat(valtarifaactual) * parseInt(element.diasviaje)) * parseFloat(multiplica)

							}
						
						} else {

							//liquida al 50%

							valtarifaactual = parseFloat(valtarifaactual) / 2
							valortotal += (parseFloat(valtarifaactual) * parseInt(element.diasviaje)) * parseFloat(multiplica)

						}

						totdias += parseInt(element.diasviaje)
						
					}

					var valviaticos = this.limpiarMoneda(this.valorviaticos)
					var valtiquetes = this.limpiarMoneda(this.valortiquetes)
					var valgastosviaje = this.limpiarMoneda(this.valorgastosviaje)

					var totalliquidaautomatico = parseFloat(valviaticos) + parseFloat(valtiquetes) + 
												parseFloat(valgastosviaje) + parseFloat(valortotal)

					this.valorliquidado = this.number_format(totalliquidaautomatico,2)
				}

			},

			editaRubroDistribuye(itemactual){

				let valoraliquidar = this.limpiarMoneda(this.valorliquidado)

				if(parseFloat(valoraliquidar) == 0){
					this.showMessage(true,3000,'error','No se ha ingresado el valor a Liquidar')	
					return
				}

				this.itemrubrodistriuido = itemactual
				this.valordistribuido =this.number_format(itemactual.valordistribuido,2)
				this.saldorubro = this.number_format(itemactual.saldo,2)
				this.dialog_editrubroliquida = true

			},

			actualizaRubroActual(){
				
				let valordistribuye = this.limpiarMoneda(this.valordistribuido)
				let valorsaldo = this.limpiarMoneda(this.saldorubro)

				if(parseFloat(valordistribuye) > parseFloat(valorsaldo)){

					this.showMessage(true,3000,'error','El Valor distribuido no puede superar el Saldo')	
					return
				}

				let totalactual = parseFloat(valordistribuye)
				let valoraliquidar = this.limpiarMoneda(this.valorliquidado)

				for (let index = 0; index < this.arrRubrosDistribuye.length; index++) {
					const element = this.arrRubrosDistribuye[index]

					if(this.itemrubrodistriuido.codigorubro != element.codigorubro){

						totalactual += parseFloat(element.valordistribuido)
					}
					
				}

				if(parseFloat(totalactual) > parseFloat(valoraliquidar)){

					this.showMessage(true,3000,'error','El Valor Total Distribuido no puede superar el valor liquidado')	
					return
				}

				for (let index = 0; index < this.arrRubrosDistribuye.length; index++) {
					const element = this.arrRubrosDistribuye[index]

					if(this.itemrubrodistriuido.codigorubro == element.codigorubro){

						element.valordistribuido = this.limpiarMoneda(this.valordistribuido)
						break
					}
					
				}

				this.totalvalordistribuido = totalactual
				this.dialog_editrubroliquida = false

			},

