				const result = await desplazamientosService.deleteFile(this.codigo,this.tipofiledelete,ruta, this.filedelete)
				
				if(result.data){
					if(result.data.tipo == 'error'){

						this.showMessage(true,4000,'error',result.data.respuesta)

					} else if(result.data.tipo == 'success'){
						
						switch (this.tipofiledelete) {

							case 'archivoarl':
								
								this.archivoarl = {file_name:'',file_path:'',file_name_tmp:''}
