							case 'archivorp':
								
								this.archivorp = {file_name:'',file_path:'',file_name_tmp:''}
								break	
							
							default:
								break
						}

						this.showMessage(true,4000,'success',result.data.respuesta)

					} else {

						this.showMessage(true,4000,'error','Se presentó un error al intentar realizar la operación')

					}
				
				} else {

					this.showMessage(true,4000,'error','Se presentó un error al intentar realizar la operación')

				}

				this.dialog_deletefile = false

