					this.showMessage(true,4000,'error','Debe seleccionar el Tipo de Liquidación')
					return
				}

				if(parseFloat(valoraliquidar) == 0){

					this.showMessage(true,4000,'error','Debe ingresar el Valor a Liquidar')
					return
				}

				if(parseFloat(this.totalvalordistribuido) != parseFloat(valoraliquidar)){

					this.showMessage(true,4000,'error','La suma del valor distribuido debe ser exacto al valor liquidado')
					return
				}

