	          		_this.loading = false          	
	          		
				})

			},

			traeProyectosAll(){

		     	var _this = this                
				
				this.arrProyectosGenAll = []

              	proyectosService.getDataXVoBo()
                  .then(function(res) {

                      if (res.request) {						  

                          if(res.data){
							 
							_this.arrProyectosGenAll = res.data.data					

                          }

                      }
                      
              	}).catch(function(err) {              
              		
				})

            },
				
			traePlanesAquisicion(){

		     	var _this = this                
				
				this.arrPlanAdqGenAll = []
				this.arrNecesidadesPlanAdq = []
				this.arrRubrosGenAll = []

				this.planadqsel = 0
				this.necesidadplaadqsel = 0
				this.selected = []

              	planesadquisicionService.getDataXProyecto(this.proyectosel)
                  .then(function(res) {

                      if (res.request) {						  

                          if(res.data){
							 
							_this.arrPlanAdqGenAll = res.data.data	


                          }

                      }
                      
              	}).catch(function(err) {              
              		
				})

            },
			
			traeNecesidadesPlan(){

		     	var _this = this                
				
				this.arrNecesidadesPlanAdq = []
				this.arrRubrosGenAll = []

				this.necesidadplaadqsel = 0
				this.selected = []

              	planesadquisicionService.getNecesidadXPlanAdq(this.planadqsel)
                  .then(function(res) {

                      if (res.request) {						  

                          if(res.data){
							 
							_this.arrNecesidadesPlanAdq = res.data.data					

                          }

                      }
                      
              	}).catch(function(err) {              
              		
				})

            },

			agregaBitacora(accion, detalle){              

				var data = {modulo:this.modulo,accion:accion,detalle:detalle}              
				this.$root.$emit('agregaBitacora', data)

            }
		},
		
    	watch: {
		    edit: function (val) {
				
				this.totaldiasviaje = 0
				this.totaltarifasviajes = this.number_format(0, 2)
				this.totalrubros = this.number_format(0, 2) 		

		       if(val)
		       {
		       		this.formTitle = 'Editar'					

                    this.codigo = this.itemactual.codigo
                    this.fecha = this.itemactual.fecha
                    this.tipobeneficiario = this.itemactual.tipobeneficiario
                    this.tipoidentcodigo = this.itemactual.tipoidentcodigo
                    this.identificacion = this.itemactual.identificacion
                    this.beneficiario = this.itemactual.beneficiario
                    this.cargocodigo = this.itemactual.cargocodigo
                    this.direccion = this.itemactual.direccion
                    this.telefono = this.itemactual.telefono
                    this.email = this.itemactual.email
                    this.fechanacimiento = this.itemactual.fechanacimiento
                    this.valorcontrato = this.number_format(this.itemactual.valorcontrato,2)
                    this.mesescontrato = this.itemactual.mesescontrato                
                    this.requieretiquetes = this.itemactual.requieretiquetes
                    this.objetivogeneral = this.itemactual.objetivogeneral                
					this.viaticos = this.itemactual.viaticos
					this.valorviaticos = this.number_format(this.itemactual.valorviaticos,2)
                    this.pagoanticipado = this.itemactual.pagoanticipado
                    this.reembolso = this.itemactual.reembolso
                    this.valortiquetes = this.number_format(this.itemactual.valortiquetes,2)               
                    this.gastosviaje = this.itemactual.gastosviaje
                    this.valorgastosviaje = this.number_format(this.itemactual.valorgastosviaje,2)
					this.observacion = this.itemactual.observacion					
					this.valorbaseben = this.number_format(this.itemactual.valorbaseben,2)
					this.enviovobo = this.itemactual.enviovobo
					this.usuario = this.itemactual.usuario

					this.voboplaneacion = this.itemactual.voboplaneacion
					this.obsplaneacion = this.itemactual.obsplaneacion
					this.vobofinanciera = this.itemactual.vobofinanciera
					this.obsfinanciera = this.itemactual.obsfinanciera
					this.vobodireccion = this.itemactual.vobodireccion
					this.obsdireccion = this.itemactual.obsdireccion

					let archivocarl = this.itemactual.archivoarl
					this.rutasarchivoarl = this.itemactual.rutaarchivoarl

					this.valorliquidado = this.number_format(this.itemactual.valorliquidado,2)
					this.tipoliquidacion = this.itemactual.tipoliquidacion
					this.archivoarl = {file_name:archivocarl,file_path:(archivocarl != '' ? this.rutasarchivoarl : ''),file_name_tmp:''}

					this.vobopresupuesto = this.itemactual.vobopresupuesto
					this.obspresupuesto = this.itemactual.obspresupuesto
					this.numerorp = this.itemactual.numerorp

					let archivocrp = this.itemactual.archivorp
					this.rutaarchivorp = this.itemactual.rutaarchivorp
					
					this.archivorp = {file_name:archivocrp,file_path:(archivocrp != '' ? this.rutaarchivorp : ''),file_name_tmp:''}

					this.arrBeneficiarios = []
					if(this.tipobeneficiario == 'C'){

						this.arrBeneficiarios = this.arrContratistas

					} else if (this.tipobeneficiario == 'T'){

						this.arrBeneficiarios = this.arrPersonas

					}
					
					this.pagotesoreria = this.itemactual.pagotesoreria

					this.traerDestinos()
					this.traerDestinosOrdenFecha()					
					this.traerRubros()
					

		       } else{

		       		this.resetForm()
					this.formTitle = 'Nuevo'					  

			   }
			   
			},
			
			editdestinos: function (val) {

		       if(val)
		       {
		       		this.formTitleDestinos = 'Editar'					

					this.codigodestino = this.itemactualdes.codigo
                    this.origennacional = this.itemactualdes.origennacional
					this.origeninternacional = this.itemactualdes.origeninternacional
					this.destinonacional = this.itemactualdes.destinonacional
					this.destinointernacional = this.itemactualdes.destinointernacional
					this.txtdestinoorigen = this.itemactualdes.txtdestinoorigen
					this.txtdestinofinal = this.itemactualdes.txtdestinofinal
					this.fechaida = this.itemactualdes.fechaida
					this.fecharegreso = this.itemactualdes.fecharegreso
					this.tipotransporte = this.itemactualdes.tipotransporte
					this.txttipotransporte = (this.itemactualdes.tipotransporte == 'F' ? 'FLUVIAL' : 
											(this.itemactualdes.tipotransporte == 'T' ? 'TERRRESTRE' : 
											'AÉREO'))

					this.tarifa = this.number_format(this.itemactualdes.tarifa,2)
					this.moneda = this.itemactualdes.moneda
					this.simbolomoneda = this.itemactualdes.simbolomoneda
					this.tasa = this.number_format(this.itemactualdes.tasa,2)
					this.tarifapesos = this.number_format(this.itemactualdes.tarifapesos,2)
					this.totaltarifa = this.itemactualdes.totaltarifa
					this.diasviaje = this.itemactualdes.diasviaje

		       } else{

		       		this.resetFormDestino()
					this.formTitleDestinos = 'Nuevo'					  

			   }
			   
			},

			arrRubrosGen: function (val) {

				this.totalrubros = 0

				let total = val.reduce((a, b) => parseFloat(a) + parseFloat(b.saldo), 0)

				this.totalrubros = total

			},

			arrDestinos: function (val) {

				this.totaldiasviaje = 0
				this.totaltarifasviajes = 0

				let totaldias = val.reduce((a, b) => parseFloat(a) + parseFloat(b.diasviaje), 0)
				let totalvalortar = val.reduce((a, b) => parseFloat(a) + parseFloat(b.tarifapesos), 0)

				this.totaldiasviaje = totaldias
				this.totaltarifasviajes = this.number_format(totalvalortar,2)

			},

			arrRubrosDistribuye: function (val) {

				this.totalvalordistribuido = 0

				let total = val.reduce((a, b) => parseFloat(a) + parseFloat(b.valordistribuido), 0)

				this.totalvalordistribuido = total

			},	

			tipodestino: function (val){				
					
				this.traeLugares()

			},

			/*valorbaseben: function(val){

				this.valorbasebennum = this.limpiarMoneda(val)

			},*/

		},
		mounted() {

            this.traerDataPrincipal()
            this.traeTiposId()
			this.traeCargos()
			this.traePersonal()
			this.traeContratistas()
			this.traeRubrosAll()
			this.traeCiudades()
			this.traePaises()
			this.traerTarifas()
			this.traeUsuariosPerfiles()
			this.traeUsuarios()
			this.traeEstados()
			this.traeProyectosAll()
			this.agregaBitacora('INGRESO','INGRESO AL MODULO')
		},		
	}
	
</script>

