			traerSubproceso(proceso){
				var _this = this
                this.loading = true
				
				this.arrSubProc = []

				switch (proceso) {
					case 1:
						
						ProyectosService.getDataAll()
						.then(function(res) {

							if (res.request) {
												
								if(res.data){
									_this.arrSubProc = res.data.data
								}

							}  
							_this.loading = false                
							
						}).catch(function(err) {

							_this.loading = false                
							
						})

						break;
					case 2:
						//Declaraciones ejecutadas cuando el resultado de expresión coincide con el valor2
						break;
				}
			},

			OnChangeProcesos(){
				var _this = this

				this.traerSubproceso(_this.proceso)

