			verata(ruta){				

				window.open(configService.apiUrl + '/getverActa?archivo=' + ruta, '_blank')
			},
			veradjunto(adjunto){	
				

				if(adjunto != ''){
					if(adjunto != null){
						window.open(configService.apiUrl + '/getveradjunto?archivo=' + adjunto, '_blank')
					}else{
						this.showMessage(true,3000,'info','No hay archivo adjunto')
					}
					
				}else{
					this.showMessage(true,3000,'info','No hay archivo adjunto')
				}
				
			},
			OnChangeSeleccion(){
				var _this = this
				var consulta = []
				var idcargo = 0
				var ind = 0
				var idarray = 0
				_this.arrAsistentes = []
				_this.arrAsistentesTotal = []

				switch (_this.Seleccionasistentes) {
					case 1:
						
						PersonalService.getData()
						.then(function(res) {

							if (res.request) {
												
								if(res.data){
									consulta = res.data.data


									consulta.forEach(function (item) { 
										
										idarray += 1
										_this.arrAsistentes.push({id :idarray, codigo: item.codigo, nombre: item.nombrecompleto})
										
										_this.arrAsistentesTotal.push({id :idarray, codigo: item.codigo, nombre: item.nombrecompleto})

									});

								}

							}  
							_this.loading = false                
							
						}).catch(function(err) {

							_this.loading = false                
							
						})
						
						break;
					case 2:
						
						PersonalService.PersonalCargos()
						.then(function(res) {

							if (res.request) {
												
								if(res.data){
									consulta = res.data.data


									consulta.forEach(function (item) { 

										
										
										if(idcargo != item.codigocargo){														
											idarray += 1
											idcargo = item.codigocargo
											_this.arrAsistentes.push({id: idarray, codigo: item.codigocargo, nombre :item.nombrecargo, children:[]})									
											
										}
										idarray += 1
										_this.arrAsistentes[_this.arrAsistentes.length - 1]["children"].push({id :idarray, codigo: item.codigo, nombre: item.nombrecompleto})
										_this.arrAsistentesTotal.push({id :idarray, codigo: item.codigo, nombre: item.nombrecompleto})


									});

								}

							}  
							_this.loading = false                
							
						}).catch(function(err) {

							_this.loading = false                
							
						})
						break;
					case 3:
						
						PersonalService.PersonalAreas()
						.then(function(res) {

							if (res.request) {
												
								if(res.data){
									consulta = res.data.data


									consulta.forEach(function (item) { 

										
										
										if(idcargo != item.codigoarea){														
											idarray += 1
											idcargo = item.codigoarea
											_this.arrAsistentes.push({id: idarray, codigo: item.codigoarea, nombre :item.nombrearea, personal:0, children:[]})									
											
										}
										idarray += 1
										_this.arrAsistentes[_this.arrAsistentes.length - 1]["children"].push({id :idarray, codigo: item.codigo, nombre: item.nombrecompleto, personal:1})
										_this.arrAsistentesTotal.push({id :idarray, codigo: item.codigo, nombre: item.nombrecompleto})


									});

								}

							}  
							_this.loading = false                
							
						}).catch(function(err) {

							_this.loading = false                
							
						})

