			EnviarActa(){

		     	var _this = this
                this.loading = true
				
				var pdfanexo = _this.reunionPdf.ruta + "/"+ _this.reunionPdf.nombrepdf

				/* Email al elaborador del acta */
				_this.dataAsist.forEach(function (item) { 
					
					var mensajecorreo = "Cordial Saludo,<br><br> " +
										"Se realizó el acta de la reunión con el titulo : " + _this.itemactual.titulo + " del día " + _this.itemactual.fecha +										
