			AgregarExternos(){
				
				var _this = this
				var mensaje = ''
				if(_this.nombreexterno == ''){
					this.showMessage(true,3000,'error','Nombre invalido')
					return
				}
				if(_this.emailexterno == ''){
					this.showMessage(true,3000,'error','Email invalido')
					return
				}else if(!_this.validEmail(_this.emailexterno)){
					this.showMessage(true,3000,'error','Email invalido')
					return
				}

				_this.dataExternos.forEach(function(itemasis){
					if(_this.nombreexterno == itemasis.NombreExterno){
						mensaje='Nombre ya registrado'
						return
					}
					if(_this.emailexterno == itemasis.EmailExterno){
						mensaje='Email ya registrado'
						return
					}
				});

				if(mensaje != ''){
					this.showMessage(true,3000,'error',mensaje)
					return
				}

				_this.dataExternos.push({NombreExterno: _this.nombreexterno, EmailExterno: _this.emailexterno})	
				_this.nombreexterno=''
				_this.emailexterno=''
			},
			
			validEmail: function (email) {
			var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
			return re.test(email);
			},

