			validaGuardar(){


				var ok = false

                if(this.titulo != this.tituloant){                                      
					ok = true
                } 
				if(this.SubProceso != this.SubProcesoAnt){                                      
					ok = true
                }
				if(this.Fechareunion != this.FechareunionAnt){                                      
					ok = true
				}
				if(this.HoraIni != this.HoraIniAnt){                                      
					ok = true
				}
				if(this.HoraFin != this.HoraFinAnt){                                      
					ok = true
				}
				if(this.lugar != this.lugarAnt){                                      
					ok = true
				}
				if(this.linkreunion != this.linkreunionAnt){                                      
					ok = true
				}
				if(this.citador != this.citadorAnt){                                      
					ok = true
                }
				if(this.ElaboActa != this.ElaboActaAnt){                                      
					ok = true
				}
				if(this.sede != this.sedeAnt){                                      
					ok = true
                }

				var Fecha1 = new Date(this.Fechareunion+' '+this.HoraIni);
				var Fecha2 = new Date(this.Fechareunion+' '+this.HoraFin);

				
				if(Fecha1 >= Fecha2){
					return 'Hora final debe ser mayor a la hora inicial'
				}

				if(this.linkreunion != ''){
					
					var url = require("url");
					var result = url.parse(this.linkreunion);
					
					if (result.hostname === null){
						return 'url de la reunión no es valida'
					}

				}

				if(ok == false){
					return 'No hay datos para actualizar'
				}else{
					 return ''
				}

				

            },

			validaGuardarReunionTareas(){


				var ok = false

                if(this.tareas != this.tareasant){                                      
					ok = true
                } 
				if(this.recursos != this.ResursosAnt){                                      
					ok = true
                }
				if(this.responsablestarea != this.responsablestareaAnt){                                      
					ok = true
				}
				if(this.FechaLimite != this.FechaLimiteAnt){                                      
					ok = true
				}
				if(this.tipo_tarea != this.tipo_tareaAnt){                                      
					ok = true
				}
				
				if(this.FechaLimite <= this.itemactual.fecha){
					return 'La fecha limite de la tarea debe ser superior a la fecha de la reunión'
				}

				if(ok == false){
					return 'No hay datos para actualizar'
				}else{
					 return ''
				}

