			saveitem(){

                var valida = this.validaGuardar()
                var datbit = ''
				var codproy = 0
				var codplaadq = 0
				var codplaadqnec = 0
				var codsol = 0
				var fileadjunto = ''

                if(valida != ''){
                    this.showMessage(true,3000,'error',valida)
                    return
                }
                
                if(this.edit){

                    if (this.itemactual.titulo != this.titulo)
                        datbit += 'Descripción: ' + this.itemactual.titulo.toUpperCase() + ' a ' + this.titulo.toUpperCase()
                        
                    datbit = 'CODIGO: ' + this.codigo + ' (' + datbit + ')'

                } else {

					datbit += 'DESCRIPCION: ' + this.titulo.toUpperCase()

				}

				var _this = this
                this.loading = true
				
				switch (_this.proceso) {
					case 1:
						
						codproy = _this.SubProceso
						
						break;
					case 2:
						
						codplaadq = _this.SubProceso

						break;
					case 3:
						
						codplaadqnec = _this.SubProceso

						break;
					case 4:
						
						codsol = _this.SubProceso

						break;
				}
				
				if(this.adjuntoreunion != null){
					fileadjunto = this.adjuntoreunion.nombrepdf
				}

                var data = {codigo:this.codigo,titulo:this.titulo,codproyecto:codproy,codplanaqui:codplaadq,codnecesidad:codplaadqnec,codsolicitud:codsol,fecha:this.Fechareunion,horaini:this.HoraIni,horafin:this.HoraFin,lugar:this.lugar,enlace:this.linkreunion,citador:this.citador,elaboracta:this.ElaboActa,sede:this.sede,adjunto:fileadjunto};
				
                ReunionesService.guardarReunion(data)
	              .then(function(res) {

	              	  if (res.response) {                           
                            
                          var error = res.response.data.message
                          _this.showMessage(true,3000,'error',error)

                      } else if(res.statusText){
	              	  		
	              	  		var mensaje = ''
                            var accbitacora = ''
                            
	              	  		if(_this.edit){
                                mensaje = 'Actualizó'
                                accbitacora = 'ACTUALIZACION'
                            } else {
                                mensaje = 'Agregó'
                                accbitacora = 'CREACION'
                            }

	              	  		_this.showMessage(true,3000,'success','Se ' + mensaje + ' la Categoría con éxito')

	              	  		_this.del_item = ''
	              	  		_this.edit = -1	              	  		
	              	  		_this.traerReuniones()
	              	  		_this.resetForm()
                            _this.dialog = false
                                
                            _this.agregaBitacora(accbitacora,datbit)

	              	  }else{	                                         	

	                  		_this.showMessage(true,3000,'error',res.message) 	                  	
	                  }

	                    _this.loading = false	                 
	                   	
	          	}).catch(function(err) {

	          		_this.showMessage(true,3000,'error',err)	    
	          		_this.loading = false          	
	          		
				})
            },
			
