                          _this.showMessage(true,3000,'error',error)

                      } else if(res.statusText){
	              	  		
	              	  		var mensaje = 'Actualizó'
                            var accbitacora = 'ACTUALIZACION'
                            var datbit = 'Reunión en el resumen '+_this.itemactual.codigo
	              	  		

	              	  		_this.showMessage(true,3000,'success','Se ' + mensaje + ' la reunión con éxito')

	              	  		_this.del_item = ''
							_this.edit = -1	 
							_this.traerReuniones()	   
	              	  		_this.resetForm()
                            _this.dialog_resumen = false
                                
                            _this.agregaBitacora(accbitacora,datbit)

	              	  }else{	                                         	

	                  		_this.showMessage(true,3000,'error',res.message) 	                  	
	                  }

	                    _this.loading = false	                 
	                   	
	          	}).catch(function(err) {

	          		_this.showMessage(true,3000,'error',err)	    
	          		_this.loading = false          	
	          		
				})
			},
			validaaprob(){
				var usuarioaprob = false
				var usuarioaprobres = false
				var _this = this

				_this.dataAsist.forEach(function(item){
					if(_this.UsuCodPersonal === item.Codigopersonal){
						_this.codasistusuario = item.codigo
						usuarioaprob = true
					}
				})
				
				if(usuarioaprob === false){									
					return 'No esta seleccionado para aprobar esta acta'
				}else{						
					console.log(_this.dataAsistResp)	
					_this.dataAsistResp.forEach(function(item){

						if(_this.UsuCodPersonal === item.codigopersonal){

							if(item.estado != null){
								usuarioaprobres = true							
							}

							
						}
					})

				}
				if(usuarioaprobres){
					return 'Ya registró respuesta a esta acta'
				}

				if(_this.ObsRechActa === '' &&  _this.OpcionActa === '2'){
					return 'Ha rechazado el acta por favor deje un comentario'	
				}

				return ''

			},
			saveitemReunionTareas(){

                var valida = this.validaGuardarReunionTareas()
                var datbit = ''


                if(valida != ''){
                    this.showMessage(true,3000,'error',valida)
                    return
                }
                
                if(this.edit_tareas){
					
					if(this.tareas != this.tareasant){                                      
						datbit += 'Tareas: ' + this.itemactual_tareas.tarea.toUpperCase() + ' a ' + this.tareas.toUpperCase()
					} 
					if(this.recursos != this.ResursosAnt){                                      
						datbit += 'recursos aprobados: ' + this.itemactual_tareas.recursos.toUpperCase() + ' a ' + this.recursos.toUpperCase()
					}
					if(this.FechaLimite != this.FechaLimiteAnt){                                      
						datbit += 'Fecha limite: ' + this.itemactual_tareas.fecha + ' a ' + this.FechaLimite
					}
					if(this.tipo_tarea != this.tipo_tareaAnt){                                      
						datbit += 'Tareas: ' + this.itemactual_tareas.tipo + ' a ' + this.tipo_tarea
					}

                                            
                    datbit = 'CODIGO: ' + this.codigo + ' (' + datbit + ')'

                } else {

					datbit += 'Tareas: ' + this.tareas.toUpperCase()

				}

				var _this = this
                this.loading = true
								

                var data = {codigo:this.codigotarea,tareas:this.tareas,recursos:this.recursos,fechalimite:this.FechaLimite,tipo:this.tipo_tarea,estado:this.estadotarea,codigoreunion:this.itemactual.codigo,responsables:this.responsablestarea};
				
                ReunionesTareaServices.guardarReunionTarea(data)
	              .then(function(res) {

	              	  if (res.response) {                           
                            
                          var error = res.response.data.message
                          _this.showMessage(true,3000,'error',error)

                      } else if(res.statusText){
	              	  		
	              	  		var mensaje = ''
                            var accbitacora = ''
                            
	              	  		if(_this.edit_tareas){
                                mensaje = 'Actualizó'
                                accbitacora = 'ACTUALIZACION'
                            } else {
                                mensaje = 'Agregó'
                                accbitacora = 'CREACION'
                            }

	              	  		_this.showMessage(true,3000,'success','Se ' + mensaje + ' la tarea con éxito')

	              	  		_this.del_item = ''
							_this.edit_tareas = -1	
							_this.codigotarea = '0'		              	  		
	              	  		_this.traertareas()
	              	  		_this.resetForm_tareas()
                            _this.dialog_tareas_addupd = false
                                
                            _this.agregaBitacora(accbitacora,datbit)

	              	  }else{	                                         	

	                  		_this.showMessage(true,3000,'error',res.message) 	                  	
	                  }

	                    _this.loading = false	                 
	                   	
	          	}).catch(function(err) {

	          		_this.showMessage(true,3000,'error',err)	    
	          		_this.loading = false          	
	          		
				})
            },

			asistente_del(itemparam){
				var _this = this

				_this.dataAsist.forEach(function(itemasis, index, object){
					if(itemparam.codigo == itemasis.codigo){
						_this.dataAsist.splice(index, 1);
					}
					
				});
			},

			visualiza_del(itemparam){
				var _this = this

				_this.dataVisualiza.forEach(function(itemasis, index, object){
					if(itemparam.codigo == itemasis.codigo){
						_this.dataVisualiza.splice(index, 1);
					}
					
				});
			}, 

			externo_del(itemparam){
				var _this = this

				_this.dataExternos.forEach(function(itemasis, index, object){
					if(itemparam.NombreExterno == itemasis.NombreExterno && itemparam.EmailExterno == itemasis.EmailExterno){
						_this.dataExternos.splice(index, 1);
					}
					
				});
			},

			saveitem_addPart(){

                var valid_addpar = ''
				var datbit = ''
				var _this = this
				var codreunion = _this.itemactual.codigo

                if(valid_addpar != ''){
                    this.showMessage(true,3000,'error',valid_addpar)
                    return
                }
                
                if(this.editaist){

                    if (this.itemactual.titulo != this.titulo)
                        datbit += 'Descripción: ' + this.itemactual.titulo.toUpperCase() + ' a ' + this.titulo.toUpperCase()
                        
                    datbit = 'CODIGO: ' + this.codigo + ' (' + datbit + ')'

                } else {

					datbit += 'DESCRIPCION: ' + this.titulo.toUpperCase()

				}				
				
				
				var data = {codigoreunion:codreunion,asistentes:this.dataAsist};

				ReunionesAsistentesServices.guardarReunionAsistentes(data)
				.then(function(res) {

					if (res.response) {                           
							
						var error = res.response.data.message
						_this.showMessage(true,3000,'error',error)

					} else if(res.statusText){
							
							var data = {codigoreunion:codreunion,visualizacion:_this.dataVisualiza};

							ReunionesVisulizaServices.guardarReunionVisualiza(data)
							.then(function(res1) {

								if (res1.response) {                           
										
									var error = res1.response.data.message
									_this.showMessage(true,3000,'error',error)

								} else if(res1.statusText){
										


										var data = {codigoreunion:codreunion,externo:_this.dataExternos};

										ReunionesExternoServices.guardarReunionExterno(data)
										.then(function(res2) {

											if (res2.response) {                           
													
												var error = res2.response.data.message
												_this.showMessage(true,3000,'error',error)

											} else if(res2.statusText){
													


													var mensaje = ''
													var accbitacora = ''
													
													mensaje = 'Agrego asistentes '
													accbitacora = 'AGREGO ASISTENTES '
													

													_this.showMessage(true,3000,'success','Se ' + mensaje + ' de la reunión con éxito')

													_this.del_item = ''
													_this.editaist = -1	              	  		
													_this.traerReuniones()
													_this.resetForm()
													_this.dialog_addpart = false
														
													_this.agregaBitacora(accbitacora,datbit)

											}else{	                                         	

													_this.showMessage(true,3000,'error',res2.message) 	                  	
											}

												_this.loading = false	                 
												
										}).catch(function(err2) {

											_this.showMessage(true,3000,'error',err2)	    
											_this.loading = false          	
											
										})

								}else{	                                         	

										_this.showMessage(true,3000,'error',res1.message) 	                  	
								}

									_this.loading = false	                 
									
							}).catch(function(err1) {

								_this.showMessage(true,3000,'error',err1)	    
								_this.loading = false          	
								
							})
							

					}else{	                                         	

							_this.showMessage(true,3000,'error',res.message) 	                  	
					}

						_this.loading = false	                 
						
				}).catch(function(err) {

					_this.showMessage(true,3000,'error',err)	    
					_this.loading = false          	
					
				})
				
                
            },

            agregaBitacora(accion, detalle){              

              var data = {modulo:this.modulo,accion:accion,detalle:detalle}
              
              this.$root.$emit('agregaBitacora', data)

           }
		},
		
    	watch: {
		    edit: function (val) {

		       if(val)
		       {
				   
		       		this.formTitle = 'Editar'

		       		this.codigo = this.itemactual.codigo
					this.titulo = this.itemactual.titulo
					this.proceso = this.itemactual.proceso
					this.traerSubproceso(this.proceso)
					this.SubProceso = this.itemactual.SubProceso	
					this.Fechareunion = this.itemactual.fecha
					this.HoraIni = this.itemactual.horainicial
					this.HoraFin = this.itemactual.horafinal
					this.lugar = this.itemactual.lugar
					this.linkreunion = this.itemactual.linkreunion
					this.citador = this.itemactual.respcitar
					this.ElaboActa = this.itemactual.respacta	                     							
					this.sede = this.itemactual.sede   			
					this.resumen = this.itemactual.resumen

					this.tituloant = this.itemactual.titulo  
					this.SubProcesoAnt = this.itemactual.SubProceso
					this.FechareunionAnt = this.itemactual.Fechareunion
					this.HoraIniAnt = this.itemactual.horainicial
					this.HoraFinAnt = this.itemactual.horafinal
					this.lugarAnt = this.itemactual.lugar
					this.linkreunionAnt = this.itemactual.linkreunion
					this.citadorAnt = this.itemactual.respcitar
					this.ElaboActaAnt = this.itemactual.respacta	                     							
					this.sedeAnt = this.itemactual.sede

					if(this.itemactual.adjunto != ''){
						if(this.itemactual.adjunto != null){
							this.adjunto = this.itemactual.adjunto
						}else{
							this.adjunto = ''
						}
					}else{
						this.adjunto = ''
					}

		       } else{

		       		this.resetForm()
		       	    this.formTitle = 'Nuevo'
		       }
			},
			editaist: function (val) {

		       if(val)
		       {
				   this.e1 = 1

		       		this.formTitle = 'Agregar participantes'

					this.codigo = this.itemactual.codigo
					this.arrAsistentes = []
					this.dataAsist=[]
					this.dataVisualiza=[]
					this.dataExternos=[]
					this.Seleccionasistentes = 0
					this.traerAsistentes()
					this.traerVisualiza()
					this.traerExterno()
		       } 
			},
			tareas_dia: function (val) {

				if(val)
				{
					this.traertareas()

				}
			},
			ordendia: function (val) {

				if(val)
				{
					this.e1_o = 1
					this.traerReunionOrden()
					this.traerReunionAgenda()


				}
			},
			edit_tareas: function (val) {

		       if(val)
		       {
					var _this = this
				   	var resparray = []
					   
					   this.formTitle_tareas = 'Editar'

					this.codigotarea = this.itemactual_tareas.codigo
					this.FechaLimite = this.itemactual_tareas.fecha
					this.tipo_tarea = this.itemactual_tareas.tipo
					this.tareas = this.itemactual_tareas.tarea
					this.recursos = this.itemactual_tareas.recursos
					this.estadotarea = this.itemactual_tareas.codestado
					

					resparray = _this.itemactual_tareas.responsables

					_this.responsablestarea=[]
					resparray.forEach(function(item){
						_this.responsablestarea.push(item.codigo)
					});

					this.FechaLimiteAnt = this.itemactual_tareas.fecha
					this.tipo_tareaAnt = this.itemactual_tareas.tipo
					this.tareasAnt = this.itemactual_tareas.tarea
					this.recursosAnt = this.itemactual_tareas.recursos
					this.estadotareaAnt = this.itemactual_tareas.codestado

					_this.responsablestareaAnt=[]
					resparray.forEach(function(item){
						_this.responsablestareaAnt.push(item.codigo)
					});

		       } else{

		       		this.resetForm_tareas()
					this.formTitle_tareas = 'Nuevo'
		       }
			},
			editOrden: function(val){
				if(val)
		       {
					var _this = this
				   	var resparray = []
					   
					this.formTitle_orden = 'Editar'
					this.codigo = this.itemactual.codigo
					this.traerAsistentes()
					this.codigoOrden = this.itemorden.codigo
					this.NumOrden = this.itemorden.numorden
					this.tematica = this.itemorden.tematica
					this.responsableOrden = this.itemorden.codigopersonal
					this.obsOrden = this.itemorden.observaciones
					


		       } else{

		       		this.resetFormOrden()
					this.formTitle_orden = 'Nuevo'
					this.codigo = this.itemactual.codigo
					this.traerAsistentes()
					
		       }
			},
			editAgen:function(val){
				if(val)
				{
					var _this = this
					var resparray = []
					
					this.formTitle_agenda = 'Editar'
					
					this.codigoAgenda = this.itemagenda.codigo
					this.numagenda = this.itemagenda.numagenda
					this.descripAgenda = this.itemagenda.descripcion											


				} else{

					this.resetFormAgenda()
					this.formTitle_agenda = 'Nuevo'
					
						
				}
			},
			apracta: function (val) {

		       if(val)
		       {
					
					if(this.itemactual.estadoacta === 1){							
						this.showMessage(true,3000,'info','Acta ya fue enviada para aprobación')	
						this.apracta = -1				
						return
					}
					if(this.itemactual.estadoacta === 3){							
						this.showMessage(true,3000,'info','Acta ya fue approbada')			
						this.apracta = -1		
						return
					}

					this.enviaraprobar=true
					this.dataAsist=[]															
					this.traerAsistentesAprob()		
					this.traerPDFActa()			
		       } 
			},

			aprrecacta: function (val) {

		       if(val)
		       {					

					if(this.itemactual.estadoacta != 1){
						this.showMessage(true,3000,'info','No hay acta pendiente para aprobación')	
						this.aprrecacta = -1				
						return
					}
					
					this.AprRechActa=true

					this.dataAsist=[]										
					this.traerAsistentesAprob()										
					this.dataAsistResp=[]
					this.traerAsistentesAprobResp(this.itemactual.codigo, this.itemactual.codigoacta)
					
					
					
					
			   } 
			   

			},
			
			histoacta: function (val) {
				if(val)
		       {																	
					this.traeractas()										
					
			   } 
			},
			histoactadet: function (val) {
				if(val)
		       {																
					this.traerAsistentesAprobResp(this.itemactual.codigo, this.itemactual_acta.codigo)
			   } 
			},
			
		},
		mounted() {

			this.traerReuniones()
			this.traerPersonal()
			this.traerSedes()			
			this.agregaBitacora('INGRESO','INGRESO AL MODULO')
			this.UsuCodPersonal = this.$session.get('codigopersonal')
		},	
	}
	
</script>

