														<span>{{ item.valortotal |  currency({
																symbol: item.moneda,
																thousandsSeparator: '.',
																fractionCount: '0',
																fractionSeparator: ',',
																symbolPosition: 'front',
																symbolSpacing: true
															})}}
														</span>
													</template>

													<template v-slot:item.tasa="{ item }">
														<span>{{ item.tasa |  currency({
																symbol: '$ ',
																thousandsSeparator: '.',
																fractionCount: '0',
																fractionSeparator: ',',
																symbolPosition: 'front',
																symbolSpacing: true
															})}}
														</span>
													</template>

													<template v-slot:item.valorpesos="{ item }">
														<span>{{ item.valorpesos |  currency({
																symbol: '$ ',
																thousandsSeparator: '.',
																fractionCount: '0',
																fractionSeparator: ',',
																symbolPosition: 'front',
																symbolSpacing: true
															})}}
														</span>
													</template>

												</v-data-table>												
