								@click:clear="limpiarBusqueda('searchcongenadd')"	
								@keyup.enter.native="traeProyectosAll"
								
							></v-text-field>
						</v-col>
						<v-col cols="4">
							<v-text-field
								v-model="searchcongenadd_nombre"
