									symbolPosition: 'front',
									symbolSpacing: true
								})}}
							</span>
						</template>		

						<template v-slot:item.tasa="{ item }">
							<span>{{ item.tasa |  currency({
									symbol: '$ ',
									thousandsSeparator: '.',
									fractionCount: '0',
									fractionSeparator: ',',
									symbolPosition: 'front',
									symbolSpacing: true
								})}}
							</span>
						</template>						

			           	<template v-slot:item.action="{ item }">
							
							<v-icon
								small
								class="mr-5"
								color="primary" 
								:disabled="loading"
								@click="seleccionarProyectoGen(item.codigo)"								
							>
								send
							</v-icon>												
												
					  	</template>	

		            </v-data-table>	
				</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="primary" dark text @click="dialog_addproyecto=false">Cancelar</v-btn>				
				</v-card-actions>
			</v-card>			
        </v-dialog>

        <v-dialog v-model="dialog_addproyecto" width="1200">
			<v-card>
				<v-card-text>	
					<v-text-field
						v-model="searchcongenadd"
						append-icon="search"
						label="Buscar"
						single-line
						hide-details
					></v-text-field>
					<v-data-table :headers="headerproyectogenAdd" :items="arrProyectosGenAll" 
						:items-per-page="5" 
						:disable-sort="true" 
						:search="searchcongenadd">						

						<template v-slot:item.valortotal="{ item }">
							<span>{{ item.valortotal |  currency({
									symbol: item.moneda,
									thousandsSeparator: '.',
									fractionCount: '0',
									fractionSeparator: ',',
									symbolPosition: 'front',
									symbolSpacing: true
								})}}
							</span>
						</template>		

						<template v-slot:item.tasa="{ item }">
							<span>{{ item.tasa |  currency({
									symbol: '$ ',
									thousandsSeparator: '.',
									fractionCount: '0',
									fractionSeparator: ',',
									symbolPosition: 'front',
									symbolSpacing: true
								})}}
							</span>
						</template>

						<template v-slot:item.valorpesos="{ item }">
							<span>{{ item.valorpesos |  currency({
									symbol: '$ ',
									thousandsSeparator: '.',
									fractionCount: '0',
									fractionSeparator: ',',
									symbolPosition: 'front',
									symbolSpacing: true
								})}}
							</span>
						</template>
						
			           	<template v-slot:item.action="{ item }">
							
							<v-icon
								small
								class="mr-5"
								color="primary" 
								:disabled="loading"
								@click="seleccionarProyectoGen(item.codigo)"								
							>
								send
							</v-icon>												
												
					  	</template>	

		            </v-data-table>	
