						<v-layout wrap column>		
							<v-form v-model="valid_necesidad" ref="form2">
								<v-flex>
									<v-textarea	
										filled															
										name="input-7-4"
										label="Descripción"
										v-model="descripcion"
										:rules="[rules.required]"
										:readonly="edit_necesidad != 0"	
									></v-textarea>									
								</v-flex>			
								<v-flex>
									<v-text-field v-model="valor" label="valor"
										:rules="[rules.required]"
