										:rules="[rules.required]"
										clearable							                    										
									>									
									</v-select>
								</v-flex>
								<v-flex>
									<v-text-field v-model="fechasolcontrata" type="date" label="Fecha de Solicitud de Contratación"
										:rules="[rules.required]"
										clearable																		
									>
									</v-text-field>
								</v-flex>	

								<v-flex>
									Estado: {{ estadonecesidadtxt }}
								</v-flex>	
								<!--<v-flex>
									<v-select
										:items="arrEstadosNecesidad"
										item-value="codigo"
										item-text="descripcion"
										v-model="estadocodigonecesidad"
										label="Estado"
										:rules="[rules.required]"
										:return-object="true"				
									>									
									</v-select>
								</v-flex>-->

							</v-form>				
						</v-layout>
					</v-container>
				</v-card-text>
  
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="primary" text @click="cerrarDialogoNecesidad">Cancelar</v-btn>
					<v-btn color="primary" text @click="saveitemnecesidad" :disabled="!valid_necesidad">{{ titulobtnuardaritem }}</v-btn>
				</v-card-actions>

          	</v-card>

        </v-dialog>

        <v-dialog v-model="dialog_necesidad_del" max-width="400px">

          	<v-card>

				<v-card-title>
					<span class="headline red--text">Eliminar Necesidad</span>
				</v-card-title>
  
				<v-card-text>					
					¿Esta seguro de eliminar la Necesidad seleccionada?. Recuerde que eliminará las 
					actividades y los códigos UNSPSC asociados a ella. 					
				</v-card-text>
  
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="primary" dark text @click="closeDialogoDelNecesidad">Cancelar</v-btn>
					<v-btn color="red" text @click="deleteItemnecesidad" >Eliminar</v-btn>
				</v-card-actions>

          	</v-card>

        </v-dialog>

		<v-dialog v-model="dialog_unspsc" persistent max-width="900px">

          	<v-card>
				<v-card-text>	
					<unspsc :planadquisicion.sync = "codigo" :necesidad.sync = "codigonecesidad"
							:descripcionnecesidad.sync = "necesidadtxt" :enviovobo.sync = "enviovobo">
					</unspsc>
				</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="primary" dark text @click="dialog_unspsc=false">Cancelar</v-btn>				
				</v-card-actions>
          	</v-card>

        </v-dialog>

