									> 						
										<template v-slot:item.valor="{ item }">
											<span>{{ item.valor |  currency({
													symbol: moneda,
													thousandsSeparator: '.',
													fractionCount: '0',
													fractionSeparator: ',',
													symbolPosition: 'front',
													symbolSpacing: true
												})}}
											</span>
										</template>

										<template v-slot:item.totalactividades="{ item }">
											<span>{{ item.totalactividades |  currency({
													symbol: moneda,
													thousandsSeparator: '.',
													fractionCount: '0',
													fractionSeparator: ',',
													symbolPosition: 'front',
													symbolSpacing: true
												})}}
