					codigopersona = this.coordinador
				
				this.$root.$emit('enviarNotificaciones', {codigopersona: codigopersona, mensajenotificacion: values.mensajenotificacion, 
														asuntocorreo: values.asuntocorreo, mensajecorreo: values.mensajecorreo,
														codigoopcion:codigoopcion,numerointerno:numerointerno})				
			},

