			async enviarParaVoBo(){

				try{
					
					let tasaactual = parseFloat(this.arrProyectosGen[0].tasa)	

					let valnecesidades = await planesadquisicionService.validaNecesidades(this.codigo)
					let valunspsc = await planesadquisicionService.ValidaUnspsc(this.codigo)
					let valactividadnecesidad = await planesadquisicionService.ValidaActividadNecesidad(this.codigo)
					let valactividadcategorias = await planesadquisicionService.ValidaActividadCategoria(this.codigo, this.basemoneda, tasaactual)

					let datavalnecesidad = valnecesidades.data[0]
					let datavalunspsc = valunspsc.data[0]

					let valtotaconvenio = parseFloat(this.limpiarMoneda(this.valortotalconvenio))

					if(parseFloat(parseFloat(datavalnecesidad.total)) <= 0){

						this.showMessage(true,5000,'error','No se ha ingresado ninguna Necesidad')
						return

					} else {

						let totactual = parseFloat(this.limpiarMoneda(datavalnecesidad.totalvalor))

						if(totactual < valtotaconvenio){

							let valdiferencia = valtotaconvenio - totactual
							this.showMessage(true,5000,'error','El total de las Necesidades no compromete el valor total del Convenio.' + 
											'Saldo: $ ' + this.number_format(valdiferencia,0))
							return

						}

					}

					if(parseFloat(parseFloat(datavalunspsc.total)) <= 0){

						this.showMessage(true,5000,'error','No se ha ingresado ningun Código Unspsc')
						return

					}

					this.arrErrActivNecesidades = valactividadnecesidad.data
					this.arrErrActivCategorias = valactividadcategorias.data
					
					if(this.arrErrActivNecesidades.length > 0 || this.arrErrActivCategorias > 0){

						this.dialog_validavobo = true
						return

					}

					let rtaactualiza = await planesadquisicionService.enviarParaVoBo(this.codigo)

						if(rtaactualiza.statusText){

							this.showMessage(true,3000,'success','Se envió para Visto Bueno el Plan de Adquiciones')     
							
							let datbit = 'Se envió para Visto Bueno Plan de Adquisiciones. Convenio ' + this.numeroconvenio +
											' - ' + this.nombreconvenio	 		

							this.agregaBitacora('VISTO BUENO',datbit)

							var mensajecorreo = "Cordial Saludo,<br><br> " +
												"Se envío para su respectivo Visto Bueno el Plan de Adquisiciones para el Acuerdo Específico # : " + 
												this.numeroconvenio + ", Nombre: " + this.nombreconvenio + 													
												"<br><br>Agradeciendo su atención,<br><br> " +                        
												"<b>SINCHI</b><br><br> " +
												"<i>Este es un servicio de notificaciones generado automáticamente por medio de " +
												"correo electrónico, no lo responda.</i> <br>"
							
							this.envioNotificacionesVoBo({mensajenotificacion:'Se envío para su respectivo Visto Bueno el Plan de Adquisiciones para el Acuerdo Específico # : ' + 
														this.numeroconvenio + ', Nombre: ' + this.nombreconvenio,  
														asuntocorreo: 'Envío para Visto Bueno de Plan de Adquisiciones para un Acuerdo Específico',
														mensajecorreo:mensajecorreo})

							this.traeNecesidadXPlanAdq()
							this.traerDataPrincipal()

							this.dialog = false

						} else {

							if(rtaactualiza.message){
								this.showMessage(true,5000,'error',rtaactualiza.message) 
							} else {

								this.showMessage(true,5000,'error','Error desconocido') 

							}	

						}

				} catch (error) {	

					this.showMessage(true,5000,'error',error)

				}
				finally {

				}

			},

			async dataVistasFinanciera(){

				try{

					let tasaactual = parseFloat(this.arrProyectosGen[0].tasa)
					
					let vistnecesidad = await planesadquisicionService.VistaFinNecesidades(this.codigo)
					let visactnec = await planesadquisicionService.VistaFinActNec(this.codigo)
					let visactcat = await planesadquisicionService.VistaFinActCat(this.codigo, this.basemoneda, tasaactual)
					let visactividades = await planesadquisicionService.VistaFinResActividades(this.codigo)

					this.vistanecesidades = vistnecesidad.data					
					this.vistaactividadnecesidad = visactnec.data
					this.vistaactividadcatalogo = visactcat.data
					this.vistaactividades = visactividades.data

					let arrtotal = (accumulator, currentValue) => parseFloat(accumulator) + parseFloat(currentValue.valor)
					const initialValue = 0

					this.valortotalnecesidades = this.vistanecesidades.reduce(arrtotal, initialValue)
					this.valortotalnecesidades = this.basemoneda == 'C' ? this.arrProyectosGen[0].moneda : '$ ' + this.number_format(this.valortotalnecesidades, 2)


				} catch (error) {	

					this.showMessage(true,5000,'error',error)

				}
				finally {

				}

			},

