			arrProyectosGen: function(val){
				
				if(val.length > 0){
					this.moneda = this.basemoneda == 'C' ? val[0].moneda : '$ '
					this.valortotalconvenio = this.basemoneda == 'C' ? val[0].valortotal : 
												(val[0].tasa != 0 ? val[0].valortotal * val[0].tasa:
												val[0].valortotal)
				}

			},
