	    methods: {
			
			showMessage (snack, time, color, text) {

			      this.$root.$emit('showSnack', {snack: snack, time: time, color: color, text: text})

			},

			async traeUsersPlaSubDirCient(){

				this.usuarioPlaneacion = 0
				this.usuarioSubDirCientifica = 0

				try{

					let planeacion = await areasService.getXCodigo(4) //planeacion
					let subdircientifica = await areasService.getXCodigo(3) //subdireccion cientifica

					if(planeacion.data)
						this.usuarioPlaneacion = planeacion.data.data[0].encargadocodigo
					
					if(subdircientifica.data)
						this.usuarioSubDirCientifica = subdircientifica.data.data[0].encargadocodigo

				} catch (error) {					
					
				}
				finally {

				}

			},

			envioNotificaciones(values){ // mensajenotificacion,asuntocorreo, mensajecorreo){
								
				//se envia notificacion al coordinador o encargado
				var codigopersona = 0
				var codigoopcion = 16 //proyectos
				var numerointerno = this.convenio				

				if(this.codigoencargado != '' && this.codigoencargado != '0')
					codigopersona = this.codigoencargado
				else
					codigopersona = this.codigocoordinador

				this.$root.$emit('enviarNotificaciones', {codigopersona: codigopersona, mensajenotificacion: values.mensajenotificacion, 
														asuntocorreo: values.asuntocorreo, mensajecorreo: values.mensajecorreo,
														codigoopcion:codigoopcion,numerointerno:numerointerno})

				//se verifica si se debe enviar a planeación y/o subdirección cientifica
				var estado = 0

				if(typeof this.codigoestado == 'object'){
					estado = this.codigoestado.codigo					

				} else {
					estado = this.codigoestado					
				}

				//si estado es formulacion se envía notificación a planeación

				if(estado=='3'){

					if(this.usuarioPlaneacion != '' && this.usuarioPlaneacion != '0'){

						this.$root.$emit('enviarNotificaciones', {codigopersona: this.usuarioPlaneacion, 
										mensajenotificacion: values.mensajenotificacion, 
										asuntocorreo: values.asuntocorreo, 
										mensajecorreo: values.mensajecorreo,codigoopcion:codigoopcion,
										numerointerno:numerointerno})

					}

				}
				
				//si el estado es ejecución de envía notificación a subdirección cientifica
				if(estado=='5'){

					if(this.usuarioSubDirCientifica != '' && this.usuarioSubDirCientifica != '0'){

						this.$root.$emit('enviarNotificaciones', {codigopersona: this.usuarioSubDirCientifica, 
										mensajenotificacion: values.mensajenotificacion, 
										asuntocorreo: values.asuntocorreo, 
