			number_format(amount, decimals) {

				amount += ''; // por si pasan un numero en vez de un string
				amount = parseFloat(amount.replace(/[^0-9\.]/g, '')) // elimino cualquier cosa que no sea numero o punto

				decimals = decimals || 0 // por si la variable no fue fue pasada

				// si no es un numero o es igual a cero retorno el mismo cero
				if (isNaN(amount) || amount === 0) 
					return parseFloat(0).toFixed(decimals)

				// si es mayor o menor que cero retorno el valor formateado como numero
				amount = '' + amount.toFixed(decimals)

				var amount_parts = amount.split('.'),
					regexp = /(\d+)(\d{3})/

				while (regexp.test(amount_parts[0]))
					amount_parts[0] = amount_parts[0].replace(regexp, '$1' + '.' + '$2')

				return amount_parts.join(',')
			},

			generaArrayUnicos(arrayEvaluar,campo){

				var NuevoArray = []

				for (let index = 0; index < arrayEvaluar.length; index++) {
					const element = arrayEvaluar[index]

					var ok = 1
					for (let k = 0; k < NuevoArray.length; k++) {
						const element2 = NuevoArray[k]

						if(element[campo] == element2[campo]){
							ok = 0
							break
						}
						
					}

					if(ok == 1){
						NuevoArray.push({codigo:element[campo]})

					}					
				}

				return NuevoArray

			},		

			saveitem(){
				
				if (this.codigoconvenio == '' || this.codigoconvenio == '0'){

					this.showMessage(true,3000,'error','Debe ingresar el Acuerdo Específico para el Proyecto')			
					return
				}
				else if (this.nombre == ''){

					this.showMessage(true,3000,'error','Debe ingresar el Nombre Corto del Proyecto')			
					return

				}
				else if (this.descripcion == ''){

					this.showMessage(true,3000,'error','Debe ingresar la Descripción del Proyecto')			
					return

				}	
				else if(this.codigoestado == ''){

					this.showMessage(true,3000,'error','Debe ingresar el Estado del Proyecto')			
					return
				}

				var _this = this
                this.loading = true

				var datbit = ''
				
				var ciudad = ''
				var nombreciudad = ''
				var estado = ''
				var nombreestado = ''
							
				if(typeof this.codigoestado == 'object'){
					estado = this.codigoestado.codigo
					nombreestado = this.codigoestado.descripcion

				} else {
					estado = this.codigoestado
					nombreestado = this.itemactual.descripcionestado
				}

				/*if(typeof this.codigociudad == 'object'){
					ciudad = this.codigociudad.codigo
					nombreciudad = this.codigociudad.nombrecompleto
				} else {

					ciudad = this.codigociudad
					nombreciudad = this.itemactual.nombreciudad

				}*/

				var pilaresPicia = []

				for (let k = 0; k < this.selectedpilpic.length; k++) {
					const element = this.selectedpilpic[k]

					pilaresPicia.push({proyecto: this.codigo, codigo:element.codigo})
					
				}

				let fechasusmarco = this.fechasuscripconmarco == '' ? '1000-01-01' : this.fechasuscripconmarco
				let fechatermarco = this.fechaterminaconmarco == '' ? '1000-01-01' : this.fechaterminaconmarco
				let fechainimarco = this.fechainicioconmarco  == '' ? '1000-01-01' : this.fechainicioconmarco

				var poa = {codigo:this.codigopoa,descripcion:this.descripcion, estrategia:this.estrategia, indicador:this.indicador, 
							unidadmedida:this.unidadmedida,metasectorial:this.metasectorial,
							metainstitucional:this.metainstitucional,observacion:this.observacion,capitulo:this.capitulo,
							linea:this.linea,objetivo:this.objetivo,ods:this.ods,meta:this.meta,proyectopenia:this.proyectopenia,
							lineainvpenia:this.lineainvpenia}

				var data = {codigo:this.codigo,descripcion:this.descripcion,nombre:this.nombre,convenio:this.convenio,
							antecedentes:this.antecedentes,codigoestado:estado,objeto:this.objeto,objetivogeneral:this.objetivogeneral,
							actores:this.actores,beneficiarios:this.beneficiarios,justificaimportancia:this.justificaimportancia,
							justificapertenencia:this.justificapertenencia,justificaimpacto:this.justificaimpacto,tipo:this.tipo,
							codigoconvenio:this.codigoconvenio,conveniomarco:this.conveniomarco}

				if(this.edit){

					var valida = this.validaSave()

					if(!valida){

						this.showMessage(true,3000,'error','No hay datos para actualizar')
						this.loading = false
						return
					}

					datbit = ''
				}

				datbit = 'No. CONVENIO: ' + this.convenio + ', NOMBRE: ' + this.nombre + ', ESTADO: ' + nombreestado

				var arrAccionPiciaN = this.generaArrayUnicos(this.selectedaccionespic,'codigo')
				var arrIndicadoresPiciaN = this.generaArrayUnicos(this.selectedindicadorespic,'codigo')
				var arrLinInvPeiN = this.generaArrayUnicos(this.selectedlininvpei,'codigo')

				ProyectosService.guardarData(poa,data,this.selectedlininvpic,arrAccionPiciaN,arrIndicadoresPiciaN,
											arrLinInvPeiN,this.selectedproginvpei,pilaresPicia)
	              .then(function(res) {

	              	  if (res.response) {                           
                            
                          var error = res.response.data.message
                          _this.showMessage(true,3000,'error',error)

                      } else if(res.statusText){
	              	  		
	              	  		var mensaje = ''
							var accbitacora = ''

	              	  		if(_this.edit){
								mensaje = 'Actualizó'
								accbitacora = 'ACTUALIZACION'
							} else {
								mensaje = 'Agregó'
								accbitacora = 'CREACION'
							}

	              	  		_this.showMessage(true,3000,'success','Se ' + mensaje + ' el Proyecto con éxito')

							var numconvenio = _this.convenio
							var nombreproyecto = _this.nombre

	              	  		_this.del_item = ''
	              	  		_this.edit = -1	              	  		
	              	  		_this.traerDataPrincipal()	              	  		
							_this.dialog = false								
							
							_this.agregaBitacora(accbitacora,datbit)
							
							var mensajecorreo = "Cordial Saludo,<br><br> " +
												"Se " + mensaje + " el proyecto con # de convenio: " + 
