                this.loading = true				
					
				this.selectedaccionespic = []
				this.arrAccionesPicia = []

				var txtFiltroLinvInv = ''

				for (let index = 0; index < this.selectedlininvpic.length; index++) {
					const element = this.selectedlininvpic[index]

					txtFiltroLinvInv += txtFiltroLinvInv != '' ? ',' + element.codigo : '(' + element.codigo
				}
				
				if(txtFiltroLinvInv != ''){

					txtFiltroLinvInv += ')'

					accionesPiciaService.getSeleccionados(this.codigo,txtFiltroLinvInv)
					.then(function(res) {

						if (res.request) {
											
							if(res.data){

								_this.arrAccionesPicia = res.data								

								for (let index = 0; index < _this.arrAccionesPicia.length; index++) {
									const element = _this.arrAccionesPicia[index];

									if(element.Activo == 1)
										_this.selectedaccionespic.push({codigo:element.codigo,descripcion:element.descripcion})										
									
								}

								_this.traeIndicadoresPicia()

							}

						}
							
						_this.loading = false                
						
					}).catch(function(err) {
	
						_this.loading = false                
						
					})
