			async valsInVoBo () {
				var arrayErr = []
				var i = 0

				var estado = ''

				if(typeof this.itemactual.codigoestado == 'object'){
					estado = this.codigoestado.codigo					

				} else {
					estado = this.codigoestado					
				}
				console.log(this.codigo)
				
				var verPilarespicia = await proyectospilarespiciaService.getDataXProyecto(this.codigo)
				var verLinInvpicia = await lineasInvPiciaService.getDataXProyecto(this.codigo)
				var verAccionespicia = await accionesPiciaService.getDataXProyecto(this.codigo)
				var verIndicadorespicia = await indicadoresPiciaService.getDataXProyecto(this.codigo)				
				var verProgInvpei = await programasInvPeiService.getDataXProyecto(this.codigo)

				var verLinInvpei = await lineasInvPeiService.getDataXProyecto(this.codigo)

				var verObjetivos = await proyectosobjetivosService.FiltroXProyectoAll(this.codigo)
				var verProductos = await proyectosproductosService.FiltroXProyectoAll(this.codigo)
				var verActividades = await proyectosactividadesService.FiltroXProyectoAll(this.codigo)
				var verCiudades = await proyectosciudadesService.FiltroXProyectoAll(this.codigo)
				var verEquipo = await proyectosequipoService.FiltroXProyectoAll(this.codigo)

				if(this.itemactual.capitulocodigo == '' || this.itemactual.capitulocodigo == '0'){

					//i++
					arrayErr.push({'errvobo':'No se ha ingresado el Capítulo en el Plan Nacional de Desarrollo'})

				}

				if(this.itemactual.lineacodigo == '' || this.itemactual.lineacodigo == '0'){

					//i++
					arrayErr.push({'errvobo':'No se ha ingresado la Línea estratégica en el Plan Nacional de Desarrollo'})

				}

				if(this.itemactual.objetivocodigo == '' || this.itemactual.objetivocodigo == '0'){

					//i++
					arrayErr.push({'errvobo':'No se ha ingresado el Objetivo en el Plan Nacional de Desarrollo'})

				}
				
				if(this.itemactual.odscodigo == '' || this.itemactual.odscodigo == '0'){

					//i++
					arrayErr.push({'errvobo':'No se ha ingresado el Objetivo de desarrollo Sostenible en el Plan Nacional de Desarrollo'})

				}

				if(this.itemactual.proginvpeniacodigo == '' || this.itemactual.proginvpeniacodigo == '0'){

					//i++
					arrayErr.push({'errvobo':'No se ha ingresado el Programa de Investigación en el Plan Estratégico Nacional de Investigación Ambiental'})

				}

				if(this.itemactual.lininvpeniacodigo == '' || this.itemactual.lininvpeniacodigo == '0'){

					//i++
					arrayErr.push({'errvobo':'No se ha ingresado la línea de Investigación en el Plan Estratégico Nacional de Investigación Ambiental'})

				}
								
				if(this.itemactual.metacodigo == '' || this.itemactual.metacodigo == '0'){

					//i++
					arrayErr.push({'errvobo':'No se ha ingresado el Indicador en el Plan Estratégico Nacional de Investigación Ambiental'})

				}

				if(verPilarespicia.length <= 0) {

					arrayErr.push({'errvobo':'No se ha ingresado ningún pilar en el Plan Institucional Cuatrienal de Investigación Ambiental'})

				}
				
				if(verLinInvpicia.length <= 0) {

					arrayErr.push({'errvobo':'No se ha ingresado ninguna Línea de Investigación en el Plan Institucional Cuatrienal de Investigación Ambiental'})

				}

				if(verAccionespicia.length <= 0) {

					arrayErr.push({'errvobo':'No se ha ingresado ninguna Acción en el Plan Institucional Cuatrienal de Investigación Ambiental'})

				}				

				if(verIndicadorespicia.length <= 0) {

					arrayErr.push({'errvobo':'No se ha ingresado ningún Indicador en el Plan Institucional Cuatrienal de Investigación Ambiental'})

				}

				if(verProgInvpei.length <= 0){

					arrayErr.push({'errvobo':'No se ha ingresado ningún Programa de Investigación en el Plan Estratégico Institucional'})

				}

				if(verLinInvpei.length <= 0){

					arrayErr.push({'errvobo':'No se ha ingresado ninguna Línea de Investigación en el Plan Estratégico Institucional'})

				}

				if (this.codigoconvenio == '' || this.codigoconvenio == '0'){

					//i++
					arrayErr.push({'errvobo':'No se ha ingresado el Acuerdo Específico para el Proyecto'})								
				}

				if (this.itemactual.nombre == ''){

					arrayErr.push({'errvobo':'No se ha ingresado el Nombre Corto del Proyecto'})			
					
				}

				if (this.itemactual.descripcion == ''){

					arrayErr.push({'errvobo':'No se haingresado la Descripción del Proyecto'})			
					
				}
				
				if(estado=='' || estado=='0'){

					arrayErr.push({'errvobo':'No se ha ingresado el Estado del Proyecto'})

				}

				if(this.itemactual.objeto==''){

					arrayErr.push({'errvobo':'No se ha ingresado el Alcance del Proyecto'})

				}

				if(this.itemactual.justificaimportancia==''){

					arrayErr.push({'errvobo':'No se ha ingresado la Importancia en la Justificación del Proyecto'})

				}

				if(this.itemactual.justificapertenencia==''){

					arrayErr.push({'errvobo':'No se ha ingresado la Pertenencia en la Justificación del Proyecto'})

				}

				if(this.itemactual.justificaimpacto==''){

					arrayErr.push({'errvobo':'No se ha ingresado el Impacto en la Justificación del Proyecto'})

				}
	
				if(this.itemactual.objetivogeneral==''){

					arrayErr.push({'errvobo':'No se ha ingresado el Objetivo General del Proyecto'})

				}

				if(this.itemactual.actores==''){

					arrayErr.push({'errvobo':'No se ha ingresado los Actores del Proyecto'})

				}

				if(this.itemactual.beneficiarios==''){

					arrayErr.push({'errvobo':'No se ha ingresado los Beneficiarios del Proyecto'})

				}

				if(this.itemactual.antecedentes==''){

					arrayErr.push({'errvobo':'No se ha ingresado los Antecedentes del Proyecto'})

				}
				
				if(verObjetivos.length <= 0) {

					arrayErr.push({'errvobo':'No se ha ingresado ningún Objetivo Específico'})

				}

				if(verProductos.length <= 0) {

					arrayErr.push({'errvobo':'No se ha ingresado ningún Producto'})

				}				
				
				if(verActividades.length <= 0) {

					arrayErr.push({'errvobo':'No se ha ingresado ninguna Actividad'})

				} else {

					for (let index = 0; index < verActividades.length; index++) {
						const element = verActividades[index];
						
						if(element.fechainicio == '' || element.fechainicio == '0000-00-00'){

							arrayErr.push({'errvobo':"No se ha ingresado la Fecha de Inicio de la Actividad No. '" +
											element.nactividad + " - " + element.descripcion + "' para el Producto '" + element.producto +
											"' del Objetivo '" + element.objetivo + "'"})

						}

						if(element.fechafinal == '' || element.fechafinal == '0000-00-00'){

							arrayErr.push({'errvobo':"No se ha ingresado la Fecha de Final de la Actividad No. '" +
											element.nactividad + " - " + element.descripcion + "' para el Producto '" + element.producto +
											"' del Objetivo '" + element.objetivo + "'"})

						}

						if(element.peso == '' || element.peso == '0'){

							arrayErr.push({'errvobo':"No se ha ingresado el Peso de la Actividad No. '" +
											element.nactividad + " - " + element.descripcion + "' para el Producto '" + element.producto +
											"' del Objetivo '" + element.objetivo + "'"})

						}
					}
				}

				if(verCiudades.length <= 0) {

					arrayErr.push({'errvobo':'No se ha ingresado ninguna Ciudad'})

				}
				
				if(verEquipo.length <= 0) {

					arrayErr.push({'errvobo':'No se ha ingresado ningún Integrante al equipo'})

				} else {

					for (let index = 0; index < verEquipo.length; index++) {
						const element = verEquipo[index];
						
						if(element.fechainicio == '' || element.fechainicio == '0000-00-00'){

							arrayErr.push({'errvobo':"No se ha ingresado la Fecha de Inicio de la Actividad No. '" +
											element.nactividad + " - " + element.descripcion + "' para el Producto '" + element.producto +
											"' del Objetivo '" + element.objetivo + "'"})

						}

						if(element.fechafinal == '' || element.fechafinal == '0000-00-00'){

							arrayErr.push({'errvobo':"No se ha ingresado la Fecha de Final de la Actividad No. '" +
											element.nactividad + " - " + element.descripcion + "' para el Producto '" + element.producto +
											"' del Objetivo '" + element.objetivo + "'"})

						}

						if(element.peso == '' || element.peso == '0'){

							arrayErr.push({'errvobo':"No se ha ingresado el Peso de la Actividad No. '" +
											element.nactividad + " - " + element.descripcion + "' para el Producto '" + element.producto +
											"' del Objetivo '" + element.objetivo + "'"})

						}
					}
				}
				return arrayErr
			},

			async validarVoBo(){

				this.loadvb = true

				if(this.vbplaneacion){
					this.vbplaneacion = false

					//se debe actualizar el VoBo en la bds

					var rta = await proyectoService.actualizaVoBo(this.codigo,this.vbplaneacion, 'planeacion')

					this.estadosVoBo()
					this.loadvb = false
					this.traerDataPrincipal()
					return
				}					

				const arrayErr = await this.valsInVoBo()
				console.log(arrayErr)

				if(arrayErr.length > 0){
					this.arrErrVoBo = arrayErr
					this.dialog_errvobo = true
					this.vbplaneacion = false
				} else {
					this.vbplaneacion = true
				}

				var rta = await proyectoService.actualizaVoBo(this.codigo,this.vbplaneacion, 'planeacion')
				
				this.estadosVoBo()
				
				this.loadvb = false

				if(this.vbplaneacion) this.showMessage(true,3000,'success','Se generó el Visto Bueno con éxito')
				this.traerDataPrincipal()
					
			},


			async validarVoBoDir(){

				this.loadvb = true

				if(this.vbdireccion){
					this.vbdireccion = false

					//se debe actualizar el VoBo en la bds

					var rta = await proyectoService.actualizaVoBo(this.codigo, this.vbdireccion, 'direccion')

					this.estadosVoBo()
					this.loadvb = false
					this.traerDataPrincipal()
					return
				}
				
				const arrayErr = await this.valsInVoBo()

				if(arrayErr.length > 0){
					this.arrErrVoBo = arrayErr
					this.dialog_errvobo = true
					this.vbdireccion = false
				} else {
					this.vbdireccion = true
				}

				var rta = await proyectoService.actualizaVoBo(this.codigo,this.vbdireccion, 'direccion')
				
				this.estadosVoBo()
				
				this.loadvb = false
				console.log(this.data)
				console.log(this.codigo)
				if(this.vbdireccion) this.showMessage(true,3000,'success','Se generó el Visto Bueno dela direccion con éxito')
				this.traerDataPrincipal()
					
			},

