														codigoopcion:codigoopcion,numerointerno:numerointerno})				
			},	

			getPermisoTasa(){

		     	var _this = this
                
				this.permisotasa = 0

              	permisosService.getOpcion(this.$session.get('usuarioid'),18)
                  .then(function(res) {

                      if (res.request) {
                                        
