				this.dialog_del = false
			},

			validaSave(){
				
				//COMO SON MUCHOS DATOS NO SE VALIDA
				return true					
			},

			number_format(amount, decimals) {

				amount += ''; // por si pasan un numero en vez de un string
				amount = parseFloat(amount.replace(/[^0-9\.]/g, '')) // elimino cualquier cosa que no sea numero o punto

				decimals = decimals || 0 // por si la variable no fue fue pasada

				// si no es un numero o es igual a cero retorno el mismo cero
				if (isNaN(amount) || amount === 0) 
					return parseFloat(0).toFixed(decimals)

				// si es mayor o menor que cero retorno el valor formateado como numero
				amount = '' + amount.toFixed(decimals)

				var amount_parts = amount.split('.'),
					regexp = /(\d+)(\d{3})/

				while (regexp.test(amount_parts[0]))
					amount_parts[0] = amount_parts[0].replace(regexp, '$1' + '.' + '$2')

				return amount_parts.join(',')
			},

			saveitem(){

				var _this = this
                this.loading = true

				var datbit = ''				
			
				var estado = ''
				var nombreestado = ''
				var coordinador = ''
				var nombrecoordinador = ''
				var moneda = ''
				var nombremoneda = ''
				
				if(typeof this.codigoestado == 'object'){
					estado = this.codigoestado.codigo
					nombreestado = this.codigoestado.descripcion

				} else {
					estado = this.codigoestado
					nombreestado = this.itemactual.descripcionestado
				}

				if(typeof this.codigocoordinador == 'object'){
					coordinador = this.codigocoordinador.codigo
					nombrecoordinador = this.codigocoordinador.nombrecompleto
				} else {

					coordinador = this.codigocoordinador
					nombrecoordinador = this.itemactual.nombrecoordinador

				}

				if(typeof this.codigomoneda == 'object'){
					moneda = this.codigomoneda.codigo		
					nombremoneda = this.codigomoneda.simbolo			
				} else {

					moneda = this.codigomoneda	
					nombremoneda = this.itemactual.moneda			

				}

				var encargado = '' 

				if(this.codigoencargado === undefined)
					encargado = 0
				else
					encargado = this.codigoencargado

				let fechasus = this.fechasuscripcion == '' ? '1000-01-01' : this.fechasuscripcion
				let fechaterm = this.fechaterejecucion == '' ? '1000-01-01' : this.fechaterejecucion
				let fechainc = this.fechaincorporacion  == '' ? '1000-01-01' : this.fechaincorporacion

                var data = {codigo:this.codigo,numero:this.numero,fechasuscripcion:this.fechasuscripcion,
                            fechaterejecucion:this.fechaterejecucion,fechaincorporacion:this.fechaincorporacion,
							valortotal:this.limpiarMoneda(this.valortotal),codigoestado:estado,codigocoordinador:coordinador,
							codigoencargado: encargado,objeto:this.objeto,fuentefinanciacion:this.fuentefinanciacion,
							cofinanciado:this.limpiarMoneda(this.cofinanciado),contrapartida:this.limpiarMoneda(this.contrapartida),
							totaldinero:this.limpiarMoneda(this.totaldinero),totalespecie:this.limpiarMoneda(this.totalespecie),
                            tiempo:this.tiempo,contactoafn:this.contactoafn,contactoaff:this.contactoaff,contactotff:this.contactotff,
							moneda:moneda,tasa:this.tasa,contactotecfuefin:this.contactotecfuefin,
							contactoadmfuefin:this.contactoadmfuefin,
							archivogeneral:this.archivogeneral,archivootda:this.archivootda,nombre:this.nombre,
                            archivocont:this.archivocont}

				if(this.edit){

					var valida = this.validaSave()

					if(!valida){

						this.showMessage(true,3000,'error','No hay datos para actualizar')
						this.loading = false
						return
					}

					datbit = ''
				} 			

				let ValidaFechas = this.validaFechas()
				if(ValidaFechas != ''){

					this.showMessage(true,5000,'error',ValidaFechas)
					this.loading = false
					return
				}

