			addFechasPI(Fecha, Tipo){

				//Tipo:1 fechas PIT, Tipo:2 fechas PIF
				
				if(Fecha){
					if(Tipo == '1'){

						var arrayActual = this.arrFechasPIT

						for (let index = 0; index < arrayActual.length; index++) {
							const element = arrayActual[index];
							
							if(element.fechapresentacion == Fecha){

								this.showMessage(true,4000,'error','La Fecha ya existe')
								this.addfechapresinftec = ''
								return
							}
						}

						this.arrFechasPIT.push({codigo:this.codigo,fechapresentacion:this.addfechapresinftec})
						this.addfechapresinftec = ''

					} else if (Tipo == '2'){

						var arrayActual = this.arrFechasPIF

						for (let index = 0; index < arrayActual.length; index++) {
							const element = arrayActual[index];
							
							if(element.fechapresentacion == Fecha){

								this.showMessage(true,4000,'error','La Fecha ya existe')
								this.addfechapresinffin = ''
								return
							}
						}

						this.arrFechasPIF.push({codigo:this.codigo,fechapresentacion:this.addfechapresinffin})

						this.addfechapresinffin = ''

					}
				}	

