			calculaTasa(valor, variable){

				let nuevoVal = this.limpiarMoneda(valor)
				let tasaVal = this.tasa
				let total = 0
				let valFormato = ''
				
				/*switch (variable) {
					case 'cofinanciado':
						
						this.cofinanciado = this.txtcofinanciado
						break

					case 'contrapartida':
						this.contrapartida = this.txtcontrapartida
						break

					case 'totalespecie':
						this.totalespecie = this.txttotalespecie
						break

					case 'totaldinero':
						this.totaldinero = this.txttotaldinero
						break

					case 'valortotal':
						this.valortotal = this.txtvalortotal
						break

					default:
						break
				}	*/			 		

				if(tasaVal == 0)
					total = nuevoVal					
				else
					total = (nuevoVal * tasaVal)

				valFormato = '$ ' + this.number_format(total,2)

				return valFormato

