			saveitemsocio(){

				for (let index = 0; index < this.arrSocios.length; index++) {
					const element = this.arrSocios[index]

					if(this.fuentefinanciacionsocio == element.fuentefinanciacion && this.itemactualsocios.fuentefinanciacion != element.fuentefinanciacion){

						this.showMessage(true,4000,'error','La Fuente de Financiación, ya fué ingresada') 
						return
					}
					
				}

				var contrapartidaact = this.limpiarMoneda(this.contrapartidasocio)
				var totaldineroact = this.limpiarMoneda(this.totaldinerosocio)
				var totalespecieact = this.limpiarMoneda(this.totalespeciesocio)

				var totalespdin = parseFloat(totaldineroact) + parseFloat(totalespecieact)

				if(parseFloat(contrapartidaact) != parseFloat(totalespdin)){

					this.showMessage(true,4000,'error','La suma del total en dinero y especie debe coincidir con el valor de la contrapartida') 
					return

				}

				var totalcontrapartida = parseFloat(this.limpiarMoneda(this.contrapartidasocio))
				var totalcofinanciado = parseFloat(this.limpiarMoneda(this.cofinanciadosocio))
				var total = parseFloat(this.limpiarMoneda(this.valortotalsocio))
				var valtotaldinero = parseFloat(this.limpiarMoneda(this.totaldinerosocio))
				var valtotalespecie = parseFloat(this.limpiarMoneda(this.totalespeciesocio))

				for (let index = 0; index < this.arrSocios.length; index++) {
					const element = this.arrSocios[index]

					if(this.itemactualsocios.fuentefinanciacion != element.fuentefinanciacion){

						totalcontrapartida += parseFloat(element.contrapartida.toString().replace('.00',''))
						totalcofinanciado += parseFloat(element.cofinanciado.toString().replace('.00',''))
						total += parseFloat(element.valortotal.toString().replace('.00',''))
						valtotaldinero += parseFloat(element.totaldinero.toString().replace('.00',''))
						valtotalespecie += parseFloat(element.totalespecie.toString().replace('.00',''))			
					}		
					
				}

				if(this.editsocios == 0){

					this.arrSocios.push({fuentefinanciacion: this.fuentefinanciacionsocio,
										valortotal: this.limpiarMoneda(this.valortotalsocio), 
										contrapartida: this.limpiarMoneda(this.contrapartidasocio),
										cofinanciado: this.limpiarMoneda(this.cofinanciadosocio),
										totaldinero: this.limpiarMoneda(this.totaldinerosocio),
										totalespecie: this.limpiarMoneda(this.totalespeciesocio)})
					
				} else {

					for (let index = 0; index < this.arrSocios.length; index++) {
						const element = this.arrSocios[index]

						if(this.itemactualsocios.fuentefinanciacion == element.fuentefinanciacion){

							element.valortotal = this.limpiarMoneda(this.valortotalsocio)
							element.contrapartida = this.limpiarMoneda(this.contrapartidasocio)
							element.cofinanciado = this.limpiarMoneda(this.cofinanciadosocio)
							element.totaldinero = this.limpiarMoneda(this.totaldinerosocio)
							element.totalespecie = this.limpiarMoneda(this.totalespeciesocio)
							element.fuentefinanciacion = this.limpiarMoneda(this.fuentefinanciacionsocio)

							
						}
					}					

				}

				this.cofinanciado = this.number_format(totalcofinanciado.toString().replace('.00',''),2)

				this.contrapartida = this.number_format(totalcontrapartida.toString().replace('.00',''),2)
				this.valortotal = this.number_format(total.toString().replace('.00',''),2)

				this.totalespecie = this.number_format(valtotalespecie.toString().replace('.00',''),2)
				this.totaldinero = this.number_format(valtotaldinero.toString().replace('.00',''),2)

				this.resetFormSocios()
				this.editsocios = -1
				this.dialogSocios = false

			},

			number_format(amount, decimals) {

				amount += ''; // por si pasan un numero en vez de un string
				amount = parseFloat(amount.replace(/[^0-9\.]/g, '')) // elimino cualquier cosa que no sea numero o punto

				decimals = decimals || 0 // por si la variable no fue fue pasada

				// si no es un numero o es igual a cero retorno el mismo cero
				if (isNaN(amount) || amount === 0) 
					return parseFloat(0).toFixed(decimals)

				// si es mayor o menor que cero retorno el valor formateado como numero
				amount = '' + amount.toFixed(decimals)

				var amount_parts = amount.split('.'),
					regexp = /(\d+)(\d{3})/

				while (regexp.test(amount_parts[0]))
					amount_parts[0] = amount_parts[0].replace(regexp, '$1' + '.' + '$2')

				return amount_parts.join(',')
			},

