					case 'totaldinerosocio':
						
						this.totaldinerosocio = valoractual
						break
					
					case 'totalespeciesocio':
						
						this.totalespeciesocio = valoractual
						break

					case 'cofinanciado':
						
						this.cofinanciado = valoractual
						break

					case 'contrapartida':
						
						this.contrapartida = valoractual
						break

					case 'totaldinero':
						
						this.totaldinero = valoractual
						break
					
					case 'totalespecie':
						
						this.totalespecie = valoractual
						break

					default:
						break
				}
				
			},

