				let ruta = '\convenios\\C' + this.codigo + '\\'

				const result = await ConveniosService.deleteFile(this.codigo,this.tipofiledelete,ruta, this.filedelete)
				
				if(result.data){
					if(result.data.tipo == 'error'){

						this.showMessage(true,4000,'error',result.data.respuesta)

					} else if(result.data.tipo == 'success'){
						
						switch (this.tipofiledelete) {

							case 'archivogeneral':
								
								this.archivogeneral = {file_name:'',file_path:'',file_name_tmp:''}
								break

							case 'archivootda':
								
								this.archivootda = {file_name:'',file_path:'',file_name_tmp:''}
								break
								
							case 'archivocont':
								
								this.archivocont = {file_name:'',file_path:'',file_name_tmp:''}
								break	
						
							default:
								break
						}

						this.showMessage(true,4000,'success',result.data.respuesta)

					} else {

						this.showMessage(true,4000,'error','Se presentó un error al intentar realizar la operación')

					}
				
				} else {

					this.showMessage(true,4000,'error','Se presentó un error al intentar realizar la operación')

				}

				this.dialog_deletefile = false

			},
		},
		
