			errorTotalContrapartida: function() {
				
				let valorContrapartida = this.limpiarMoneda(this.contrapartida)
				let totdinero = this.limpiarMoneda(this.totaldinero)
				let totespecie = this.limpiarMoneda(this.totalespecie)

				if((parseFloat(totdinero) + parseFloat(totespecie)) > parseFloat(valorContrapartida))
					return 'El Total en Especie y en Dinero, no puede superar el valor de la Contrapartida'

				return ''
			},
			
			tiempoejecucion: function(){

				if(this.fechasuscripcion != "" && this.fechaterejecucion != ""){
					var fecha1 = new Date(this.fechaterejecucion)
					var fecha2 = new Date(this.fechasuscripcion)

					let diferencia = fecha1.getTime() - fecha2.getTime()
					var dias = diferencia / (1000*60*60*24)

					this.tiempo = dias

					return dias

				} else {

					this.tiempo = 0
					return 0

				}

			},

			errorTotalContrapartidasocio: function() {

